import Vue from 'vue'
import Vuex from 'vuex'
import VueRouter from 'vue-router'
import Toasted from 'vue-toasted'
import Raven from 'raven-js'
import RavenVue from 'raven-js/plugins/vue'

import Course from 'assets/js/course'
import Navbar from 'components/navbar'
import FeatureNotice from 'components/feature-notice'
import SecondaryNavbar from './secondary-navbar'
import SidebarSelector from './courseware/sidebar-selector'
import CoursewareList from './courseware/courseware-list'
import CoursewareDetail from './courseware/courseware-detail'
import CoursewareIndex from './courseware/courseware-index'
import BoardList from './board-list'
import VoteList from './vote-list'
import { COURSE_STATUS_NORMAL } from 'assets/js/consts/common'

require('./index.less')

window.Promise = require('bluebird')

Vue.config.productionTip = false

const axios = require('axios')

axios.defaults.headers.common.X_REQUESTED_WITH = 'XMLHttpRequest'
axios.defaults.xsrfHeaderName = 'X-CSRFTOKEN'
axios.defaults.xsrfCookieName = 'csrftoken'

Vue.prototype.$raven = Raven
if (process.env.NODE_ENV === 'production') {
  Raven.config('http://216e3d581cd9424fad7f5d8c046b2f64@sentry.xdf100.com/6').addPlugin(RavenVue, Vue).install()
}

Vue.use(Vuex)
Vue.use(VueRouter)
Vue.use(Toasted)


const router = new VueRouter({
  mode: 'history',
  base: process.env.NODE_ENV === 'production' ? '/pad/teach' : '/static/w/pages/teaching.html',
  routes: [
    {
      path: '', redirect: '/coursewares',
    },
    {
      path: '/coursewares',
      component: CoursewareIndex,
      children: [
        {
          path: '',
          name: 'courseware-list',
          components: { sidebar: SidebarSelector, 'main-content': CoursewareList },
        },
        {
          path: ':cwId',
          name: 'courseware-detail',
          components: { sidebar: CoursewareList, 'main-content': CoursewareDetail },
          props: { sidebar: { sidebarView: true } },
        },
      ],
    },
    { path: '/board-list', component: BoardList, name: 'board-list' },
    { path: '/vote-list', component: VoteList, name: 'vote-list' },
  ],
  linkActiveClass: 'active',
})


const store = new Vuex.Store({
  state: {
    teacher: null,
    courses: [], // 未归档的课程
    courseIdMap: {},
    coursewares: [],
    totalPage: 0,
    selectedPage: 1,
    teachingCourseId: 0,
    teachingStartTime: 0,
    selectedChapterId: 0,
  },
  getters: {
    school: state => (state.teacher ? state.teacher.school : null),
    classes: state => (state.teacher ? state.teacher.classes : []),
  },
  mutations: {
    updateTeacher(state, payload) {
      state.teacher = payload.teacher
      state.courses = []
      const courseIdMap = {}
      for (const courseData of payload.teacher.courses) {
        const course = new Course(courseData)
        if (course.isNormal) {
          state.courses.push(course)
          courseIdMap[course.id] = course
        }
      }
      state.courseIdMap = courseIdMap
    },
    updateAccessToken(state, payload) {
      state.accessToken = payload.accessToken
    },
    updateCoursewares(state, coursewares) {
      state.coursewares = coursewares
    },
    updatePagination(state, payload) {
      state.totalPage = payload.totalPage
      state.selectedPage = payload.selectedPage
    },
    deleteCourseware(state, deleteCourseware) {
      const index = state.coursewares.indexOf(deleteCourseware)
      if (index >= 0) {
        state.coursewares.splice(index, 1)
      }
    },
    updateCoursewareName(state, payload) {
      for (const cw of state.coursewares) {
        if (cw.id === payload.cwId) {
          cw.name = payload.name
        }
      }
    },
    updateTeachingCourse(state, payload) {
      state.teachingCourseId = payload.courseId
      state.teachingStartTime = payload.startTime
    },
    updateSelectedChapterId(state, chapterId) {
      state.selectedChapterId = chapterId
    },
  },
  actions: {
  },
})


router.beforeEach((to, from, next) => {
  if (!store.state.teacher) {
    axios.get('/pad/j/teacher_info').then((response) => {
      const { teacher, accessToken } = response.data
      store.commit('updateTeacher', { teacher })
      store.commit('updateAccessToken', { accessToken: `Bearer ${accessToken}` })
      if (process.env.NODE_ENV === 'production') {
        Raven.setUserContext({
          username: teacher.name,
          id: teacher.id,
        })
      }
      next()
    })
  } else {
    next()
  }
})


new Vue({
  delimiters: ['${', '}'],
  el: '#app',
  store,
  router,
  data: {},
  components: { Navbar, SecondaryNavbar, FeatureNotice },
})



