import _ from 'lodash'
import parser from './richtext'
import { Analysis, HtmlAnswer } from './q-common'

class Solution {
  constructor({ id, textHtml, analysisData, answerData }) {
    this.id = id
    this.textHtml = textHtml

    this.analyses = []
    this.methods = []
    this.knowledges = []

    this.isEmpty = textHtml ? false : true
    for (const data of analysisData) {
      const params = Analysis.extractParams(data)
      const analysis = new Analysis(params)
      if (!analysis.isEmpty) {
        if (analysis.methods.length) {
          this.methods = _.union(this.methods, analysis.methods)
        }
        if (analysis.knowledges.length) {
          this.knowledges = _.union(this.knowledges, analysis.knowledges)
        }
        this.analyses.push(new Analysis(params))
        this.isEmpty = false
      }
    }
    this.answers = []
    for (const data of answerData) {
      const params = HtmlAnswer.extractParams(data)
      this.answers.push(new HtmlAnswer(params))
      this.isEmpty = false
    }
  }
  static extractParams(sourceData) {
    return {
      id: sourceData.id,
      textHtml: parser(sourceData.solution_text),
      analysisData: sourceData.analyses,
      answerData: sourceData.answers,
    }
  }
}


class SubQuestion {
  constructor({ id, seqId, stemHtml, solutionData }) {
    this.id = id
    this.seqId = seqId
    this.stemHtml = stemHtml
    this.solutions = []
    for (const data of solutionData) {
      const params = Solution.extractParams(data)
      const solution = new Solution(params)
      if (!solution.isEmpty) {
        this.solutions.push(new Solution(params))
      }
    }
  }

  static extractParams(sourceData) {
    return {
      id: sourceData.id,
      seqId: sourceData.seq_id,
      answer: sourceData.answer,
      stemHtml: parser(sourceData.question_text),
      solutionData: sourceData.solutions,
    }
  }
}


export default class ScienceResponse {
  constructor({ id, kind, kindName, stemHtml, subjectId, subQuestionData, score, difficulty, favorited }) {
    this.id = id
    this.kind = kind
    this.key = `key-${kind}-${id}`
    this.kindName = kindName
    this.stemHtml = stemHtml
    this.subjectId = subjectId
    this.score = score
    this.subQuestions = []
    for (const data of subQuestionData) {
      const params = SubQuestion.extractParams(data)
      this.subQuestions.push(new SubQuestion(params))
    }
    this.subQuestions.sort((a, b) => a.seqId - b.seqId)
    this.subQuestionCount = this.subQuestions.length
    this.isSubjective = true

    this.difficulty = difficulty
    this.favorited = favorited
    this.hasReport = false
    this.unansweredSids = []
    this.results = []
  }
  static extractParams(sourceData) {
    return {
      id: sourceData.id,
      kind: sourceData.kind,
      kindName: sourceData.kind_name,
      stemHtml: parser(sourceData.stem_text),
      subjectId: sourceData.subject_id,
      score: sourceData.score,
      subQuestionData: sourceData.questions,
      difficulty: sourceData.difficulty,
      favorited: sourceData.has_collected,
    }
  }

  injectReportData(data) {
    if (data.kind !== this.kind || data.id !== this.id) {
      return
    }
    this.unansweredSids = data.unansweredSids
    this.results = data.results
    this.hasReport = true
  }
}



