<template>
  <div class="paper-container">
    <h3 class="paper-title">试题组：{{paper.name}}</h3>
    <div class="actions">
      <div class="ui toggle checkbox">
        <input type="checkbox" tabindex="0" class="hidden" v-model="showAnswer">
        <label>客观题直接显示答案</label>
      </div>
    </div>
    <div class="question-list" v-if="questions.length">
      <template v-for="(question, idx) of questions">
        <component
          :is="currentView(question)"
          :question="question"
          :index="idx + 1"
          :show-answer="showAnswer"
          :key="question.uuid"></component>
        <div class="ui divider"></div>
      </template>
    </div>
    <div v-else>
      <loader v-if="loading"></loader>
    </div>
  </div>
</template>

<script>
import Loader from 'components/loader'
import axios from 'axios'
import { getModelByDkind } from '@/question-bank/utils'
import { getViewByDkind } from '@/question-bank/preview/utils'

export default {
  name: 'new-paper',
  props: {
    paper: { type: Object, required: true },
  },
  data() {
    return {
      loading: false,
      questions: [],
      showAnswer: false,
    }
  },
  created() { this.fetchPaperQuestions() },
  components: {
    Loader,
  },
  mounted() {
    $(this.$el.querySelectorAll('.toggle.checkbox')).checkbox()
  },
  watch: {
    paper() { this.fetchPaperQuestions() },
  },
  methods: {
    currentView(question) {
      return getViewByDkind(question.displayKind)
    },
    fetchPaperQuestions() {
      this.loading = true
      return axios.get(`/question/j/papers/${this.paper.id}/questions`).then(
        (response) => {
          this.questions = []
          for (const questionData of response.data.questions) {
            const QuestionModel = getModelByDkind(questionData.displayKind)
            this.questions.push(new QuestionModel(questionData))
          }
          this.loading = false
          this.$nextTick(() => { MathJax.Hub.Queue(['Typeset', MathJax.Hub]) })
        },
      )
    },
  },
}
</script>

<style lang="less">
@import "~@/question-bank/preview/index.less";
</style>

<style lang="less" scoped>
.question-list {
  font-family: Palatino, Times, Arial, SimSun, "Microsoft YaHei", "微软雅黑", STHeiti, sans-serif;
  line-height: 1.8;
  height: 100%;
  font-size: 14px;

  .divider:last-child {
    display: none;
  }
}

.paper-container {
  padding: 1em 2em 2em 2em;
  min-height: 60vh;

  .paper-title {
    font-size: 1.3em;
    margin: .5em 0;
  }

  .actions {
    margin-bottom: 1em;
    padding: 0 0 .8em 0;
    border-bottom: solid 1px #eee;
    color: #888;
    font-size: .95em;
    .ui.toggle.checkbox {
      label {
        padding-left: 3.3rem;
        color: #888;
      }
    }
  }
  border-radius: 1rem;
}

.ui.label.tooltip {
  border: none;
}

</style>



