<template>
<div class="question question-blank" v-if="question">
  <div class="question-stem">
    <span v-if="index">{{index}}. </span>
    <span class="question-kind-label">{{question.kindName}}</span>
    <span v-if="question.score">({{question.score}}分)</span>
    <span v-if="!answerSheetMode" v-html="question.stemHtml" class="richtext"></span>
  </div>
  <div class="sub-questions">
    <div class="sub-question" v-for="(subQ, idx) of question.subQuestions">
      <span class="sub-q-seq-label" v-if="question.subQuestions.length > 1">({{idx + 1}})</span>
      <div class="sub-q-body">
        <div>
          <span v-html="showUserAnswerHtml(subQ)" v-if="showUserAnswer && question.hasReport" class="label user-answer richtext"></span>
          <span class="label">正确答案：</span>
          <span v-for="answer of subQ.answers" :key="answer.id" class="sub-q-answer">{{answer.text}}</span>
        </div>
      </div>
      <div class="q-stat" v-if="showStat && question.hasReport">
        <div class="stat-chart">
          <bar-chart class="normal content"
            :blankQuestion="subQ"
            :totalStuCount="totalStuCount"
            v-if="question.hasReport"></bar-chart>
          <div class="overlay content"></div>
          <button class="ui primary button action content" @click="resultModalTarget=subQ">查看详情</button>
        </div>
      </div>
    </div>
  </div>
  <template v-if="!answerSheetMode">
    <toggle-analysis :question="question" v-if="question.subQuestions.length > 1"></toggle-analysis>
    <toggle-analysis :question="question.subQuestions[0]" :hasSub="false" v-if="question.subQuestions.length === 1"></toggle-analysis>
  </template>
  <modal-blank-results
    :question="resultModalTarget"
    :allStudents="allStudents"
    v-if="showStat"
    @close="resultModalTarget=null"></modal-blank-results>
</div>
</template>

<script>
import BarChart from 'components/questions/bar-chart'
import ModalBlankResults from 'components/questions/modal-blank-results'
import ToggleAnalysis from 'components/questions/toggle-analysis'

require('assets/css/richtext.less')
require('./question.less')

export default {
  name: 'question-blank',
  props: {
    index: { type: Number, default: 0 },
    question: { type: Object, required: true },
    showStat: { type: Boolean, required: false, default: false },
    showAnswer: { type: Boolean, required: false, default: true },
    showUserAnswer: { type: Boolean, required: false, default: false },
    studentId: { type: Number, default: null },
    answerSheetMode: { type: Boolean, required: false, default: false },
    allStudents: { type: Object, required: false, default: null },
  },
  data() {
    return {
      resultModalTarget: null,
    }
  },
  components: { BarChart, ModalBlankResults, ToggleAnalysis },
  computed: {
    totalStuCount() {
      if (!this.allStudents) return 0
      return Object.keys(this.allStudents).length
    },
  },
  methods: {
    showUserAnswerHtml(subQuestion) {
      const userResult = subQuestion.getUserAnswer(this.studentId)
      if (userResult.correct === 1) {
        return `<span class="user-answer-correct">${userResult.answerText} <i class="ui checkmark icon"></i></span>`
      }
      if (userResult.correct === 0) {
        return `<span class="user-answer-wrong">${userResult.answerText} <i class="ui remove icon"></i></span>`
      }
      return `<span>${userResult.answerText}</span>`
    },
  },
}
</script>

<style lang="less">
@import "~assets/css/base-variables.less";
.question-blank {
  .sub-question {
    display: flex;
    .sub-q-seq-label {
      width: 2rem;
    }
    .sub-q-body {
      flex: 1;
    }
    .bar-chart {
      width: 140px;
    }
  }
  .label.user-answer {
    min-width: 200px;
    display: inline-block;
    .user-answer-correct {
      font-weight: bold;
      color: @color-teal;
    }
    .user-answer-wrong {
      font-weight: bold;
      color: @color-red;
    }
  }
}

</style>



