<template>
  <video controls controlsList="nodownload">
    <source :src="fileUrl" type="video/mp4">
    您的浏览器版本过低，请更新
  </video>
</template>

<script>

export default {
  name: 'page-video',
  props: {
    fileUrl: { type: String, required: true },
  },
  data() {
    return {}
  },
  mounted() {
    const videoEl = this.$el

    window.addEventListener('resize', () => {
      videoEl.style.maxWidth = `${videoEl.parentNode.clientWidth - 20}px`
      videoEl.style.maxHeight = `${videoEl.parentNode.clientHeight - 20}px`
    })

    this.$nextTick(() => {
      videoEl.style.maxWidth = `${videoEl.parentNode.clientWidth - 20}px`
      videoEl.style.maxHeight = `${videoEl.parentNode.clientHeight - 20}px`
    })
  },
}
</script>

<style lang="less" scoped>
video {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
</style>



