import parser from './richtext'
import { ChoiceSubQuestionNoStem as SubQuestion } from './q-common'

class Cloze {
  constructor({ id, kind, kindName, stemHtml, subjectId, subQuestionData, score, difficulty, favorited }) {
    this.id = id
    this.kind = kind
    this.key = `key-${kind}-${id}`
    this.kindName = kindName
    this.stemHtml = stemHtml
    this.subjectId = subjectId
    this.score = score
    this.subQuestions = []
    for (const data of subQuestionData) {
      const params = SubQuestion.extractParams(data)
      this.subQuestions.push(new SubQuestion(params))
    }
    this.subQuestions.sort((a, b) => a.seqId - b.seqId)
    this.subQuestionCount = this.subQuestions.length

    this.difficulty = difficulty
    this.favorited = favorited
    this.hasReport = false
  }
  static extractParams(sourceData) {
    return {
      id: sourceData.id,
      kind: sourceData.kind,
      kindName: sourceData.kind_name,
      stemHtml: parser(sourceData.stem),
      subjectId: sourceData.subject_id,
      subQuestionData: sourceData.questions,
      score: sourceData.score,
      difficulty: sourceData.difficulty,
      favorited: sourceData.has_collected,
    }
  }

  injectReportData(data) {
    if (data.kind !== this.kind || data.id !== this.id) {
      return
    }
    const subQuestionResult = data.subQuestionResult
    for (const subQuestion of this.subQuestions) {
      subQuestion.injectReportData(subQuestionResult[subQuestion.id])
    }
    this.hasReport = true
  }
}

export default Cloze



