import parser from './richtext'
import { TextAnswer as Answer, Knowledge } from './q-common'


class SubQuestion {
  constructor({ id, kind, seqId, answerData, analysisHtml, knowledgeData }) {
    this.id = id
    this.kind = kind
    this.seqId = seqId
    this.analysisHtml = analysisHtml
    this.answers = []
    for (const data of answerData) {
      const params = Answer.extractParams(data)
      this.answers.push(new Answer(params))
    }
    this.knowledges = []
    for (const data of knowledgeData) {
      const params = Knowledge.extractParams(data)
      if (params.isValid) {
        this.knowledges.push(new Knowledge(params))
      }
    }

    this.hasReport = false
  }

  static extractParams(sourceData) {
    return {
      id: sourceData.id,
      kind: sourceData.kind,
      seqId: sourceData.seq_id,
      answerData: sourceData.answers,
      analysisHtml: parser(sourceData.analysis),
      knowledgeData: sourceData.knowledges || [],
    }
  }

  injectReportData(data) {
    this.unansweredSids = data.unansweredSids || []
    this.correctSids = data.correctSids || []
    this.wrongResults = data.wrongResults || []
    this.hasReport = true
  }

  getUserAnswer(sid) {
    if (!this.hasReport) {
      return
    }
    if (this.correctSids.indexOf(sid) >= 0) {
      return { correct: 1, answerText: this.answers[0].text }
      // 多个正确答案的时候，此处要兼容
    }
    for (const result of this.wrongResults) {
      if (sid === result.sid) {
        return { correct: 0, answerText: result.answer }
      }
    }
    return { correct: -1, answerText: '未作答' }
  }
}


export default class Blank {
  constructor({ id, kind, kindName, stemHtml, subjectId, subQuestionData, score, difficulty, favorited }) {
    this.id = id
    this.kind = kind
    this.key = `key-${kind}-${id}`
    this.kindName = kindName
    this.stemHtml = stemHtml
    this.subjectId = subjectId
    this.score = score
    this.subQuestions = []
    for (const data of subQuestionData) {
      const params = SubQuestion.extractParams(data)
      this.subQuestions.push(new SubQuestion(params))
    }
    this.subQuestions.sort((a, b) => a.seqId - b.seqId)
    this.subQuestionCount = this.subQuestions.length

    this.difficulty = difficulty
    this.favorited = favorited
    this.hasReport = false
  }

  static extractParams(sourceData) {
    return {
      id: sourceData.id,
      kind: sourceData.kind,
      kindName: sourceData.kind_name,
      stemHtml: parser(sourceData.stem_text),
      subjectId: sourceData.subject_id,
      score: sourceData.score,
      subQuestionData: sourceData.questions,
      difficulty: sourceData.difficulty,
      favorited: sourceData.has_collected,
    }
  }

  injectReportData(data) {
    if (data.kind !== this.kind || data.id !== this.id) {
      return
    }
    const subQuestionResult = data.subQuestionResult
    for (const subQuestion of this.subQuestions) {
      subQuestion.injectReportData(subQuestionResult[subQuestion.id])
    }
    this.hasReport = true
  }
}



