<template>
  <div class="courseware-list" id="courseware-list" :class="sidebarView?'side-list':'main-list'">
    <div class="courseware-container" id="courseware-container">
      <div class="heading">
        <button v-if="sidebarView" class="link link-primary"
          @click="$router.push({name: 'courseware-list'})">&lt;&lt;返回课件库首页</button>
        <template  v-if="!sidebarView">
          <h2 class="container-title flex-1">课件库</h2>
        </template>
      </div>
      <div class="content-empty" v-if="!loading && coursewares.length===0">
        <img :src="require('assets/img/content-empty.png')">
        <p>
          什么也没有，试试<span class="text-danger">改变或减少筛选条件</span>
        </p>
      </div>
      <loader v-if="loading"></loader>
      <div class="coursewares">
        <div class="courseware-block" v-for="cw of coursewares" :key="'courseware-'+cw.id">
          <div class="cw-cover">
            <img :src="require('assets/img/icon-cw.png')">
          </div>
          <div class="cw-detail" @click="showCwDetail(cw)">
            <h2 class="cw-title">{{cw.name}}</h2>
            <div class="cw-pages">
              <p class="text-muted" v-if="cw.pages[3].length">
                <span class="class-kind-label">课前预习（{{cw.pages[3].length}}）</span>
                <template v-for="page in cw.pages[3]">{{page.target.name}} / </template>
              </p>
              <p class="text-muted" v-if="cw.pages[2].length">
                <span class="class-kind-label">上课课件（{{cw.pages[2].length}}）</span>
                <template v-for="page in cw.pages[2]">{{page.target.name}} / </template>
              </p>
              <p class="text-muted" v-if="cw.pages[1].length">
                <span class="class-kind-label">课后作业（{{cw.pages[1].length}}）</span>
                <template v-for="page in cw.pages[1]">{{page.target.name}} / </template>
              </p>
            </div>
          </div>
          <div class="cw-action" v-if="!sidebarView">
            <button class="ui icon basic button trash" @click="deleteItem=cw" data-tooltip="删除" data-position="top center" data-variation="small">
              <i class="trash outline icon"></i>
            </button>
            <button class="ui icon basic button slide-show" data-tooltip="播放课件" data-position="top center" data-variation="small" @click="playCw(cw)">
              <icon name="slide-show"></icon>
            </button>
          </div>
        </div>
      </div>
      <pagination class="pagination"
        :totalPage="totalPage" :curPage="selectedPage"
        @update:selected="(page) => {fetchCoursewares(page)}"
        v-show="!loading && coursewares.length"></pagination>
    </div>
    <modal v-if="deleteItem" class="item-delete" :withHeader="false" @close="deleteItem=null">
      <div slot="body">
        <span class="ui text-center circular label">
          <i class="ui red warning icon"></i>
        </span>
        <p class="alert-text">确认要删除 <strong>{{deleteItem.name}}</strong> 课件包吗？删除后不可恢复。</p>
      </div>
      <div slot="footer">
        <button class="ui basic button" @click="deleteItem=null">取消</button>
        <button class="ui red button" @click="actionDelItem()">删除</button>
      </div>
    </modal>
  </div>
</template>

<script>
import { mapState } from 'vuex'
import axios from 'axios'
import Modal from 'components/modal'
import Loader from 'components/loader'
import 'components/icons/slide-show'
import Icon from 'components/icons/Icon'
import Pagination from 'components/pagination'

export default {
  name: 'courseware-list',
  props: {
    sidebarView: { type: Boolean, default: false },
  },
  data() {
    return {
      loading: false,
      deleteItem: null,
    }
  },
  mounted() {
    this.fetchCoursewares()
  },
  components: { Loader, Icon, Pagination, Modal },
  watch: {
    selectedChapterId() {
      this.fetchCoursewares(1)
    },
    loading(val, oldVal) {
      if (val && !oldVal) {
        const windowTop = $(window).scrollTop()
        if (windowTop >= 118) {
          $(window).scrollTop(118)
        }
      }
    },
  },
  computed: {
    ...mapState(['coursewares', 'totalPage', 'selectedPage', 'courses', 'selectedChapterId']),
  },
  methods: {
    actionDelItem() {
      if (!this.deleteItem) {
        return
      }
      axios.delete(`/pad/j/coursewares/${this.deleteItem.id}/delete`).then((response) => {
        if (response.data.status === 0) {
          this.$toasted.success('删除成功', { duration: 1000, position: 'bottom-center' })
          this.$store.commit('deleteCourseware', this.deleteItem)
          this.deleteItem = null
        } else if (response.data.status === 1) {
          this.$toasted.error(response.data.detail, { duration: 1000, position: 'bottom-center' })
        }
      })
    },
    showCwDetail(cw) {
      this.$router.push({ name: 'courseware-detail', params: { cwId: cw.id } })
    },
    fetchCoursewares(page = 1) {
      this.loading = true
      const params = {
        page,
        pageSize: 10,
        chapterId: this.selectedChapterId,
      }
      axios.get('/pad/j/coursewares', { params }).then((response) => {
        this.$store.commit('updateCoursewares', response.data.coursewares)
        this.$store.commit('updatePagination', { totalPage: response.data.totalPage, selectedPage: page })
        this.loading = false
      })
    },
    playCw(cw) {
      window.open(`/pad/coursewares/${cw.id}/detail`, '_blank')
    },
  },
}
</script>

<style lang="less">
.item-delete {
  text-align: center;
}
.item-delete .ui.circular.label {
  border: solid 1px #dd4b39;
  border-radius: 3em;
  font-size: 2em;
  width: 1.8em;
  height: 1.8em;
  background-color: white;
  .ui.icon {
    margin: 0;
  }
}
</style>

<style lang="less" scoped>
.flex-1 {
  flex: 1;
}

.field.radio-checkbox {
  cursor: pointer;
}
.side-list.courseware-list {
  float: left;
  width: 20rem;
  .heading .link {
    font-size: 1rem;
    margin-top: 1rem;
    margin-left: 1rem;
    margin-bottom: 1rem;
  }
  .courseware-block {
    padding: 1rem 0;
    margin: 0 1rem;
    .cw-cover {
      width: 1.3rem;
    }
    .cw-title {
      font-size: 1.2rem;
      margin-bottom: 4px;
    }
    .cw-pages {
      p {
        font-size: 12px;
      }
    }
    .cw-detail {
      margin: -1rem 0;
      padding: 1rem 0;
    }
  }
}
.main-list.courseware-list {
  margin-left: 26rem;
  width: ~"calc(100% - 26rem)";
}
.main-list .heading {
  display: flex;
  margin: 1.5rem 2rem;
  .container-title {
    margin: 0;
    padding: 0;
    line-height: 1;
  }
}
.courseware-list {
  position: relative;
  .courseware-container {
    width: 100%;
    display: inline-block;
    background-color: white;
    box-shadow: 0 1px 1px rgba(0,0,0,0.15);
    position: relative;
    min-height: 100vh;
  }
}
.coursewares .courseware-block:first-child {
  border-top: none;
}
.courseware-block {
  border-top: solid 1px #eee;
  padding: 1.5rem 2rem;
  display: flex;
  .cw-cover {
    width: 3.5rem;
    margin-right: 1rem;
    padding-top: 4px;
    img {
      width: 100%;
      box-shadow: -1px 1px 2px rgba(0, 0, 0, 0.15);
    }
  }
  .cw-title {
    font-size: 1.4rem;
    margin: 0 0 .5em 0;
    word-break: break-all;
  }
  .cw-pages p {
    font-size: 13px;
    margin: 0 0 4px 0;
    .class-kind-label {
      color: #666;
    }
  }
  .cw-detail{
    margin: -1.5rem 0;
    padding: 1.5rem 0;
    flex: 1;
    cursor: pointer;
    &:hover, &.focus, &:active{
      .cw-title {
        color: #42A5F5;
      }
    }
  }
  .cw-action {
    width: 8rem;
    text-align: right;
    margin-top: -.5em;
    .ui.button[data-position="top center"][data-tooltip]:after {
      font-size: 11px;
      padding: .5em;
    }
    .ui.icon.basic.button {
      margin-top: 5px;
      padding: .5em .3em;
      box-shadow: none !important;
      font-size: 16px;
      margin-right: 0;
      .fa-icon {
        font-size: 14px;
        margin-right: 0;
      }
      visibility: hidden;
      &.slide-show {
        visibility: visible;
      }
    }
  }
}

.courseware-block:hover {
  .ui.icon.basic.button {
    visibility: visible;
  }
}

</style>



