<template>
<div class="question question-liberal-material" v-if="question">
  <div class="question-stem">
    <span v-if="index">{{index}}. </span>
    <span class="question-kind-label">{{question.kindName}}</span>
    <span v-if="question.score">({{question.score}}分)</span>
    <span v-html="question.stemHtml" class="richtext"></span>
    <template v-for="(subQ, qIdx) of question.subQuestions" v-if="subQ.stemHtml">
      <br>
      <template v-if="hasMultiSubQ">({{qIdx + 1}})</template>
      <span v-html="subQ.stemHtml" class="richtext"></span>
    </template>
  </div>
  <toggle-analysis :customSlot="true" v-if="showStat">
    <span slot="toggleTitle">作答详情</span>
    <span slot="subTitle" v-if="question.hasReport && totalStuCount">{{question.results.length}}人已作答, {{totalStuCount - question.results.length}}人未作答</span>
    <template slot="toggleContent">
      <inline-subjective-results
        :question="question"
        :allStudents="allStudents"></inline-subjective-results>
    </template>
  </toggle-analysis>
  <subjective-result v-if="showUserAnswer"
    :question="question"
    :studentId="studentId"
    :student="allStudents[studentId]"></subjective-result>

  <toggle-analysis :customSlot="true">
    <span slot="toggleTitle">查看解析</span>
    <div slot="toggleContent" :class="hasMultiSubQ?'sub-questions':''">
      <div class="sub-question display-flex" v-for="(subQ, qIdx) of question.subQuestions">
        <span v-if="hasMultiSubQ" class="sub-q-seq-label">({{qIdx + 1}})</span>
        <div class="flex-1">
          <div v-if="subQ.answers && subQ.answers.length" class="display-flex">
            <span class="section-sm-label">答案：</span>
            <div>
              <span v-for="answer of subQ.answers" v-html="answer.textHtml" class="sub-q-answer richtext"></span>
            </div>
          </div>
          <div v-if="subQ.analysisHtml" class="display-flex">
            <span class="section-sm-label">解析：</span>
            <div v-html="subQ.analysisHtml" class="richtext"></div>
          </div>
          <div v-if="subQ.knowledges && subQ.knowledges.length" class="display-flex">
            <span class="section-sm-label">考点：</span>
            <div>
              <span class="ui knowledge label" v-for="knowledge in subQ.knowledges">{{knowledge.name}}</span>
            </div>
          </div>
        </div>
      </div>
    </div>
  </toggle-analysis>
</div>
</template>

<script>
import InlineSubjectiveResults from 'components/questions/inline-subjective-results'
import SubjectiveResult from 'components/questions/subjective-result'
import ToggleAnalysis from 'components/questions/toggle-analysis'

require('assets/css/richtext.less')
require('./question.less')

export default {
  name: 'question-liberal-material',
  props: {
    index: { type: Number, default: 0 },
    question: { type: Object, required: true },
    showStat: { type: Boolean, required: false, default: false },
    showAnswer: { type: Boolean, required: false, default: true },
    showUserAnswer: { type: Boolean, required: false, default: false },
    studentId: { type: Number, default: null },
    answerSheetMode: { type: Boolean, required: false, default: false },
    allStudents: { type: Object, required: false, default: null },
  },
  data() {
    return {}
  },
  components: { InlineSubjectiveResults, SubjectiveResult, ToggleAnalysis },
  computed: {
    totalStuCount() {
      if (!this.allStudents) return 0
      return Object.keys(this.allStudents).length
    },
    hasMultiSubQ() {
      if (!this.question || this.question.subQuestions.length < 2) return false
      return true
    },
    studentResult() {
      if (this.question && this.question.results) {
        for (const result of this.question.results) {
          if (this.studentId === result.sid) {
            return result
          }
        }
      }
    },
  },
}
</script>



