<template>
<div class="send-time-picker">
  <input type="text" class="underlay-input">
  <div class="time-picker-input" @click="showPicker()">
    <span class="time-text">{{displayValue}}</span>
    <span class="icon"><icon name="timer"></icon></span>
  </div>
  <span @click="clearTime" v-if="value && canClear"><i class="ui red trash outline icon"></i></span>
</div>
</template>

<script>

import { Mandarin } from 'flatpickr/dist/l10n/zh'
import Icon from 'components/icons/Icon'
import 'components/icons/timer'
import { PadDateTime } from 'assets/js/utils'

const flatpickr = require('flatpickr')

/*
  设置定时任务的时间空间，可设置的范围为15天内。
*/
export default {
  name: 'time-picker',
  props: {
    enableTime: { type: Boolean, default: true },
    canClear: { type: Boolean, default: true },
    autoHideInput: { type: Boolean, default: true },
    placeHolderText: { type: String, default: '' },
    value: { default: null }, // 时间
    initOffsetMinutes: { type: Number, default: 5 },
    minDate: { default: null },
    maxDate: { default: null },
    noCalendar: { type: Boolean, default: false },
    altFormat: { type: String, default: 'Y-m-d H:i:S' },
    dateFormat: { type: String, default: 'Y-m-d H:i:S' },
    defaultDate: { default: null },
  },
  data() {
    return {
      inputElement: null,
    }
  },
  computed: {
    displayValue() {
      if (!this.value) {
        return this.placeHolderText
      }
      if (this.noCalendar) {
        return this.value
      }
      if (this.altFormat === 'Y-m-d H:i:S') {
        return new PadDateTime(this.value).formatDateTime()
      }
      if (this.altFormat === 'Y-m-d') {
        return new PadDateTime(this.value).formatDate()
      }
    },
  },
  components: { Icon },
  mounted() {
    let minDate = null
    let maxDate = null
    if (!this.noCalendar) {
      if (this.minDate) {
        // 保证 minDate 没有秒后面的零数，为了绕过 flatpickr 的一个 bug
        const minDateStr = new PadDateTime(this.minDate).formatDateTime()
        minDate = new Date(minDateStr)
        // 起步时间：定在之后的5的倍数的分钟数上面
        const offset = this.initOffsetMinutes > 5 ? this.initOffsetMinutes : 5
        const minutes = minDate.getMinutes() + offset - (minDate.getMinutes() % 5)
        minDate.setMinutes(minutes)
      }
      if (this.maxDate) {
        if (minDate) {
          maxDate = new Date(new Date(this.maxDate) > minDate ? this.maxDate : minDate)
        } else {
          maxDate = new Date(this.maxDate)
        }
      }
    } else {
      minDate = this.minDate
      maxDate = this.maxDate
    }

    this.inputElement = this.$el.querySelector('input')

    flatpickr(this.inputElement, {
      locale: Mandarin,
      enableTime: this.enableTime,
      minDate,
      maxDate,
      defaultDate: this.defaultDate || this.value || minDate,
      time_24hr: true,
      altFormat: this.altFormat,
      dateFormat: this.dateFormat,
      noCalendar: this.noCalendar,
      clickOpens: false,
      onChange: (selectedDates, dateStr, instance) => {
        if (!this.noCalendar) {
          const d = dateStr ? new Date(dateStr) : null
          if (d && d < minDate) {
            instance.setDate(minDate, true)
            return
          }
        }
        this.$emit('input', dateStr)
      },
    })
  },
  beforeDestroy() {
    if (this.inputElement._flatpickr) {
      this.inputElement._flatpickr.destroy()
    }
  },
  methods: {
    showPicker() {
      if (this.inputElement._flatpickr) {
        this.inputElement._flatpickr.open()
      }
    },
    clearTime() {
      if (this.inputElement._flatpickr) {
        this.inputElement._flatpickr.clear()
      }
    },
  },
}
</script>
<style src="flatpickr/dist/themes/material_blue.css"></style>

<style lang="less">
  @import "~assets/css/flatpickr-override.less";
</style>

<style lang="less" scoped>
@color-blue: #42A5F5;

.send-time-picker {
  position: relative;
  display: inline-block;
}
input[type=text].underlay-input, .ui.form input[type=text] .underlay-input{
  line-height: 1.5;
  max-width: 15em;
  z-index: -1;
  opacity: 0 !important;
  position: absolute;
  &.flatpickr-input {
  }
}

.time-picker-input {
  display: inline-block;
  line-height: 1.5;
  width: 15em;
  border-bottom: solid 1px #aaa;
  cursor: pointer;
  .fa-icon {
    float: right;
  }
  .time-text {
    margin-left: .5em;
  }
  &:hover {
    color: @color-blue;
    border-bottom: solid 1px @color-blue;
  }
}
.fa-icon {
  font-size: 1.3em;
  margin-right: 0;
  cursor: pointer;
}
.ui.trash.icon {
  font-size: 1.3em;
}
</style>



