<template>
  <div class="toggle-analysis" v-if="!customSlot">
    <span class="link link-default" @click="toggleContent">
      <template v-if="showContent">
        收起解析<i class="ui angle up icon"></i>
      </template>
      <template v-else>
        查看解析<i class="ui angle down icon"></i>
      </template>
    </span>
    <div class="toggle-content" style="display: none;" v-if="!hasSub">
      <div v-if="question.answer" class="display-flex">
        <span class="section-sm-label">答案：</span>
        <span class="flex-1">{{question.answerLabel}}</span>
      </div>
      <div v-if="question.answers && question.answers.length" class="display-flex">
        <span class="section-sm-label">答案：</span>
        <div class="flex-1">
          <span v-for="answer of question.answers" class="sub-q-answer">{{answer.text}}</span>
        </div>
      </div>
      <div v-if="question.analysisHtml" class="display-flex">
        <span class="section-sm-label">解析：</span>
        <span v-html="question.analysisHtml" class="flex-1 richtext"></span>
      </div>
      <div v-if="question.knowledges && question.knowledges.length" class="display-flex">
        <span class="section-sm-label">考点：</span>
        <div class="flex-1">
          <span class="ui knowledge label" v-for="knowledge in question.knowledges">{{knowledge.name}}</span>
        </div>
      </div>
    </div>
    <div class="toggle-content sub-questions" style="display: none;" v-else>
      <div v-for="(subQ, idx) of question.subQuestions" class="sub-question">
        <span class="sub-question-seq-label">({{idx + 1}})</span>
        <div class="sub-question-body">
          <div v-if="subQ.answer" class="display-flex">
            <span class="section-sm-label">答案：</span>
            <span class="flex-1">{{subQ.answerLabel}}</span>
          </div>
          <div v-if="subQ.answers && subQ.answers.length" class="display-flex">
            <span class="section-sm-label">答案：</span>
            <div class="flex-1">
              <span v-for="answer of subQ.answers" class="sub-q-answer">{{answer.text}}</span>
            </div>
          </div>
          <div v-if="subQ.analysisHtml" class="display-flex">
            <span class="section-sm-label">解析：</span>
            <span v-html="subQ.analysisHtml" class="richtext flex-1"></span>
          </div>
          <div v-if="subQ.knowledges && subQ.knowledges.length" class="display-flex">
            <span class="section-sm-label">考点：</span>
            <div class="flex-1">
              <span class="ui knowledge label" v-for="knowledge in subQ.knowledges" :key="knowledge.id">{{knowledge.name}}</span>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
  <div class="toggle-analysis" v-else>
    <span class="link link-default" @click="toggleContent">
      <slot name="toggleTitle"></slot><i class="ui angle up icon" :class="showContent?'up':'down'"></i>
    </span>
    <span class="sub-title" @click="toggleContent">
      <slot name="subTitle"></slot>
    </span>

    <div class="toggle-content" style="display: none;">
      <slot name="toggleContent"></slot>
    </div>
  </div>
</template>

<script>
require('assets/css/richtext.less')
require('./question.less')

/*
question: 可以是小题，也可以是大题。
*/
export default {
  name: 'toggle-analysis',
  props: {
    question: { type: Object, required: false },
    hasSub: { type: Boolean, required: false, default: true },
    customSlot: { type: Boolean, required: false, default: false },
  },
  data() {
    return {
      showContent: false,
    }
  },
  components: {},
  computed: {
  },
  methods: {
    toggleContent() {
      this.showContent = !this.showContent
      $(this.$el.querySelector('.toggle-content')).slideToggle()
    },

  },
}
</script>

<style lang="less">
.toggle-analysis {
  .toggle-content p {
    margin-top: 0;
  }
  .sub-question {
    display: flex;
    .sub-question-seq-label {
      width: 2rem;
    }
    .sub-question-body {
      flex: 1;
    }
  }
}

</style>

<style lang="less" scoped>
@color-blue: #42A5F5;
@base-font-family: "Helvetica Neue",Helvetica,"PingFang SC","Hiragino Sans GB","Microsoft YaHei","微软雅黑",Arial,sans-serif;

.toggle-analysis {
  margin-top: 4px;
}
.link.link-default, .section-sm-label, .ui.label, .sub-title {
  font-family: @base-font-family;
}
.ui.label {
  font-size: 12px;
  padding: 4px 8px;
}
.link.link-default {
  font-size: 13px;
}
.link.link-default i.icon {
  margin-right: 0;
}

.toggle-content {
  margin-top: .5em;
  margin-bottom: .5em;
  padding: .5em 1em;
  border-left: 2px solid #D3DCE6;
  background-color: #fafafa;
}

.sub-title {
  color: @color-blue;
  float: right;
  cursor: pointer;
  font-size: 13px;
}

</style>



