<template>
  <div class="sidebar-tree">
    <div>
      <span class="text-muted">根据章节筛选</span>
      <chapter-selector key="sidebar-selector" class="sidebar-selector"
        bookSource="platform"
        @selectChapter="updateChapter"
        :initFirstSelected="false"></chapter-selector>
    </div>
  </div>
</template>

<script>
import ChapterSelector from 'components/chapter-selector'

export default {
  name: 'sidebar-selector',
  data() {
    return {}
  },
  mounted() {
    $(window).scroll(() => {
      const windowTop = $(window).scrollTop()
      if (windowTop > 118) {
        $('.sidebar-tree').addClass('fixed')
      }
      if (windowTop < 118) {
        $('.sidebar-tree').removeClass('fixed')
      }
    })
  },
  components: { ChapterSelector },
  methods: {
    updateChapter(chapterId) {
      this.$store.commit('updateSelectedChapterId', chapterId)
    },
  },
}
</script>

<style lang="less" scoped>
.sidebar-tree {
  display: inline-block;
  background-color: white;
  box-shadow: 0 1px 1px rgba(0,0,0,0.15);
  z-index: 10;
  height: 100vh;
  width: 25rem;
  position: absolute;
  padding: .5rem;
  .sidebar-selector {
    height: 85vh;
  }
  .component-resource-bucket {
    box-shadow: -2px -3px 3px rgba(0,0,0,0.15);
    position: fixed;
    width: 25rem;
    margin-left: -.5rem;
    bottom: 0;
  }
}
.sidebar-tree.fixed {
  position: fixed;
  top: 0;
}
</style>

<style lang="less">
.sidebar-tree {
  .book-tree {
      max-height: ~"calc(100% - 40px)";
  }
}
</style>



