<template>
  <div class="question nq-objective" v-if="question">
    <div class="q-content">
      <div>
        <span class="label">{{question.seqId}}.</span>
        <span class="question-kind-label">{{question.kindName}}</span>
        <span v-if="question.score" class="para-text">({{question.score}}分)</span>
        <span class="para-text">{{question.note}}</span>
      </div>
      <div v-if="!showStat && !showUserAnswer" class="seq-label-list">
        <span v-for="option in question.options" :key="option.id" :class="outlineOptionClass(option)">
          {{option.seqLabel}}
        </span>
      </div>
      <div class="q-stat" v-if="showStat && question.hasReport">
        <div class="stat-chart">
          <bar-chart class="normal content"
            :options="question.options"
            :totalStuCount="totalStuCount"></bar-chart>
          <div class="overlay content"></div>
          <button class="ui primary button action content" @click="resultModalTarget=question">查看详情</button>
        </div>
        <modal-objective-results
          :question="resultModalTarget"
          :allStudents="allStudents"
          v-if="showStat && resultModalTarget && resultModalTarget.hasReport"
          @close="resultModalTarget=null"></modal-objective-results>
      </div>
      <div v-if="showUserAnswer">
        <div class="options">
          <div v-for="option in question.options"
            :class="optionClass(option)"
            :key="option.id">
            <span class="seq-label">{{option.seqLabel}}</span>
            <i class="ui icon" v-if="showUserAnswer && option.isUserAnswer(studentId)"
              :class="option.isAnswer?'checkmark':'remove'"></i>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import BarChart from 'components/questions/bar-chart'
import ModalObjectiveResults from 'components/questions/modal-objective-results'

require('assets/css/richtext.less')
require('./question.less')

export default {
  name: 'nq-objective',
  props: {
    question: { type: Object, required: true },
    showStat: { type: Boolean, required: false, default: false },
    showAnswer: { type: Boolean, required: false, default: true },
    showUserAnswer: { type: Boolean, required: false, default: false },
    studentId: { type: Number, default: null },
    stemBriefly: { type: Boolean, required: false, default: false },
    allStudents: { type: Object, required: false, default: null },
  },
  data() {
    return {
      resultModalTarget: null,
    }
  },
  components: { ModalObjectiveResults, BarChart },
  computed: {
    totalStuCount() {
      if (!this.allStudents) return 0
      return Object.keys(this.allStudents).length
    },
  },
  methods: {
    optionClass(option) {
      const classes = ['option']
      if (option.isAnswer) {
        classes.push('option-correct')
      }
      if (this.showUserAnswer) {
        classes.push('with-user')
        const isUserAnswer = option.isUserAnswer(this.studentId)
        if (isUserAnswer) {
          classes.push('user-answer')
        }
        if (!option.isAnswer && isUserAnswer) {
          classes.push('option-wrong')
        }
      }
      return classes.join(' ')
    },
    outlineOptionClass(option) {
      const classes = ['seq-label-outline']
      if (option.multiAnswer) {
        classes.push('seq-label-square')
      }
      if (this.showAnswer && option.isAnswer) {
        classes.push('option-correct')
      }
      return classes.join(' ')
    },
  },
}
</script>

<style lang="less" scoped>
@import "~assets/css/base-variables.less";
.seq-label-outline {
  font-family: @base-font-family;
  display: inline-block;
  margin: .5em;
  width: 1.6em;
  line-height: 1.5em;
  text-align: center;
  border: solid 1px @color-gray;
  border-radius: 1em;
  &.seq-label-square {
    border-radius: 2px;
  }
  &.option-correct {
    background-color: @color-teal;
    color: white;
    border: solid 1px @color-teal
  }
}
.seq-label-list .seq-label-outline:first-child {
  margin-left: 1em;
}
</style>

<style lang="less" scoped>
.q-stat {
  margin-top: 4px;
  margin-left: 0;
  padding-left: 0;
  width: 160px;
}
.nq-objective .options .option {
  min-width: 4em;
  margin-right: 1em;
  .ui.icon {
    margin-left: -.5em;
  }
}
.option .seq-label {
  font-weight: normal;
}
</style>



