<template>
  <div class="paper-container">
    <h3 class="paper-title">(旧)试题组：{{paper.name}}</h3>
    <div class="question-list" v-if="questions.length">
      <template v-for="(question, idx) of questions">
        <component
          :is="currentView(question)"
          v-bind="{index: idx + 1, question: question}"
          :key="question.key"></component>
        <div class="ui divider"></div>
      </template>
    </div>
    <div v-else>
      <loader v-if="loading"></loader>
    </div>
  </div>
</template>

<script>
import Loader from 'components/loader'
import axios from 'axios'
import { getQuestionByData, QuestionKindToMV } from 'assets/js/questions/view-mode'

const components = {}
for (const item of Object.values(QuestionKindToMV)) {
  components[item.viewName] = item.view
}
export default {
  name: 'new-question',
  props: {
    paper: { type: Object, required: true },
  },
  data() {
    return {
      loading: false,
      questions: [],
    }
  },
  created() { this.fetchPaperQuestions() },
  components: {
    Loader,
    ...components,
  },
  watch: {
    paper() { this.fetchPaperQuestions() },
  },
  methods: {
    currentView(question) {
      if (!QuestionKindToMV) return ''
      return QuestionKindToMV[question.kind].view
    },
    fetchPaperQuestions() {
      this.loading = true
      return axios.get('/pad/j/fetch/paper/questions', { params: { paperId: this.paper.id } }).then(
        (response) => {
          this.questions = []
          for (const questionData of response.data.questions) {
            const q = getQuestionByData(questionData)
            this.questions.push(q)
          }
          this.loading = false
        },
      )
    },
  },
}
</script>

<style lang="less" scoped>
@import '~assets/css/base-variables.less';

.question-list {
  font-family: Palatino, Times, Arial, SimSun, "Microsoft YaHei", "微软雅黑", STHeiti, sans-serif;
  line-height: 1.8;
  height: 100%;
  font-size: 14px;

  .divider:last-child {
    display: none;
  }

}

.paper-container {
  padding: 1em 2em 2em 2em;
  min-height: 60vh;

  .paper-title {
    font-size: 1.3em;
    margin: .5em 0;
    padding-bottom: 1em;
    border-bottom: solid 1px #eee;
  }
}

.ui.label.tooltip {
  border: none;
}

</style>



