
class NqMultiChoiceOption {
  constructor({ seqId, isAnswer = false }) {
    this.id = seqId
    this.seqId = seqId
    this.seqLabel = String.fromCharCode(64 + seqId)
    this.isAnswer = isAnswer
    this.multiAnswer = true
    this.hasReport = false
  }

  injectReportData(data) {
    this.sids = data[this.seqId] || []
    this.hasReport = true
  }

  isUserAnswer(sid) {
    if (this.hasReport) {
      return this.sids.indexOf(sid) >= 0
    }
  }
}

export default class NqMultiChoice {
  constructor({ id, seqId, kind, note, score, optionCount, answer }) {
    this.id = id
    this.seqId = seqId
    this.kind = kind
    this.kindName = '多选题'
    this.multiAnswer = true
    this.key = `key-${kind}-${id}`
    this.note = note
    this.score = score

    this.answers = []
    this.answerLabels = []
    const sourceAnswerText = answer.toString()
    for (const a of sourceAnswerText) {
      const answerSeq = parseInt(a)
      this.answers.push(answerSeq)
      this.answerLabels.push(String.fromCharCode(64 + answerSeq))
    }

    this.options = []

    for (let idx = 1; idx <= optionCount; idx++) {
      const option = new NqMultiChoiceOption({ seqId: idx, isAnswer: this.answers.indexOf(idx) >= 0 })
      this.options.push(option)
    }

    this.hasReport = false
  }

  static extractParams(sourceData) {
    return {
      id: sourceData.seqId,
      seqId: sourceData.seqId,
      kind: sourceData.kind,
      note: sourceData.note,
      score: sourceData.score,
      optionCount: sourceData.optionCount,
      answer: sourceData.answer,
    }
  }

  injectReportData(data) {
    if (data.kind !== this.kind || data.seqId !== this.seqId) {
      return
    }
    this.unansweredSids = data.unansweredSids || []
    for (const option of this.options) {
      option.injectReportData(data.results)
    }
    this.hasReport = true
  }
}



