<template>
<div class="new-question-container">
  <div class="file-container">
    <component
      v-if="showFile && currentView"
      :is="currentView.type"
      v-bind="currentView.props"
      :key="currentView.key"></component>
  </div>
  <div class="right-container">
    <h3 class="nq-title">互动资料：{{nq.name}}</h3>
    <div class="actions">
      <div class="ui toggle checkbox">
        <input type="checkbox" tabindex="0" class="hidden" v-model="showAnswer">
        <label>显示答案</label>
      </div>
      <div class="ui toggle checkbox" v-if="nq.analysisFileUrl">
        <input type="checkbox" tabindex="0" class="hidden" v-model="showAnalysisFile">
        <label>查看解析文件</label>
      </div>
    </div>
    <div class="question" v-for="question of questions" :key="question.key">
      <nq-objective
        v-if="question.kind === 2026"
        class="nq-objective-wrapper"
        v-bind="{question: question, showAnswer: showAnswer}"
        :key="question.key"></nq-objective>
      <nq-subjective
        v-if="question.kind === 2027"
        class="nq-subjective-wrapper"
        v-bind="{question: question, showAnswer: showAnswer}"
        :key="question.key"></nq-subjective>
    </div>
  </div>
</div>
</template>

<script>
import axios from 'axios'
import qs from 'qs'
import PageDocument from 'components/doc/office'
import PageImage from 'components/doc/image'
import PageVideo from 'components/doc/video'
import PageAudio from 'components/doc/audio'
import { getQuestionByData } from 'assets/js/questions/view-mode'
import NqObjective from 'components/questions/nq-objective'
import NqSubjective from 'components/questions/nq-subjective'

export default {
  name: 'new-question',
  props: {
    nq: { type: Object, required: true },
  },
  data() {
    return {
      questions: [],
      showFile: false,
      showAnswer: false,
      showAnalysisFile: false,
    }
  },
  created() { this.fetchNqData() },
  components: {
    PageDocument, PageImage, PageVideo, PageAudio, NqObjective, NqSubjective,
  },
  mounted() {
    const nqContainer = this.$el
    $(nqContainer.querySelectorAll('.toggle.checkbox')).checkbox()
    const fileContainer = this.$el.querySelector('.file-container')

    window.addEventListener('resize', () => {
      fileContainer.style.width = `${nqContainer.clientWidth * 2 / 3}px`
      fileContainer.style.height = `${nqContainer.clientHeight}px`
    })
    this.$nextTick(() => {
      fileContainer.style.width = `${nqContainer.clientWidth * 2 / 3}px`
      fileContainer.style.height = `${nqContainer.clientHeight}px`
      this.showFile = true
    })
  },
  computed: {
    currentView() {
      if (!this.nq) {
        return null
      }
      const ft = this.showAnalysisFile ? this.nq.analysisFileType : this.nq.fileType
      const url = this.showAnalysisFile ? this.nq.analysisFileUrl : this.nq.fileUrl
      const key = this.showAnalysisFile ? 'nq-analysis-file' : 'nq-material-file'

      let type = 'page-document'
      switch (ft) {
        case 2020:
        case 2024:
        case 2028:
        case 2021:
          type = 'page-document'; break
        case 2022:
          type = 'page-image'; break
        case 2004:
          type = 'page-video'; break
        case 2023:
          type = 'page-audio'; break
        default:
          return null
      }
      return {
        type,
        props: { fileUrl: url },
        key,
      }
    },
  },
  methods: {
    fetchNqData() {
      axios.get('/pad/j/fetch/new_question', {
        params: { newQuestionId: this.nq.id, fields: ['questions'] },
        paramsSerializer: params => qs.stringify(params, { arrayFormat: 'repeat' }),
      }).then((response) => {
        if (response.data.status === 1) {
          this.$toasted.error(response.data.detail, { duration: 1000, position: 'bottom-center' })
        } else if (response.data.status === 0) {
          const questionArrData = response.data.nq.questions
          this.questions = []
          for (const questionData of questionArrData) {
            const q = getQuestionByData(questionData)
            this.questions.push(q)
          }
        }
      })
    },
  },
}
</script>

<style lang="less" scoped>
.new-question-container {
  width: 100%;
  height: 90vh;
  min-height: 500px;
  max-height: 1200px;
  display: flex;
  .file-container {
    position: relative;
    max-height: 100%;
    flex: 3;
    background-color: #5a6066;
  }
  .right-container {
    .nq-title {
      font-size: 1.3em;
      margin: .5em 0;
    }
    .actions {
      margin-bottom: 1em;
      padding: 0 0 .8em 0;
      border-bottom: solid 1px #eee;
      color: #888;
      font-size: .95em;
      .ui.toggle.checkbox {
      label {
        padding-left: 3.3rem;
        color: #888;
      }
      }
    }
    border-radius: 1rem;
    overflow-y: auto;
    flex: 2;
    max-height: 100%;
    overflow-y: auto;
    padding: 12px;
    min-width: 18rem;
  }
  .question {
    margin-bottom: 1.2em;
  }
}
</style>



