<template>

  <page-modal
    v-if="hasQuestions(previewItem)"
    class="modal-large"
    @close="$emit('close')" >
    <template slot="content">
      <component
        :is="previewItemView.type"
        v-bind="previewItemView.props"
        :key="previewItem.id"
        v-if="previewItemView"> </component>
    </template>
  </page-modal>

  <page-modal
    v-else-if="isDocument(previewItem) && previewItem.fileType !== 2004"
    class="page-document"
    :class="modalSizeByDoc(previewItem)"
    @close="$emit('close')" >
    <template slot="content">
      <h3 class="doc-title" :class="'file-type-' + previewItem.fileType">{{previewItem.name}}</h3>
      <div class="doc-file">
        <component
          :is="previewItemView.type"
          v-bind="previewItemView.props"
          :key="previewItem.id"
          v-if="previewItemView"> </component>
        </div>
    </template>
  </page-modal>

  <!-- 视频 -->
  <page-modal
    v-else-if="isDocument(previewItem) && previewItem.fileType===2004"
    class="page-video"
    @close="$emit('close')">
    <template slot="content">
      <header class="inbody-title"><span>{{ previewItem.name }}</span></header>
      <video controls controlsList="nodownload">
        <source :src="this.previewItem.fileUrl" type="video/mp4">
        您的浏览器版本过低，请更新
      </video>
    </template>
  </page-modal>

</template>

<script>
import Modal from 'components/modal'
import PageModal from 'components/page-modal'
import PageDocument from 'components/doc/office'
import PageImage from 'components/doc/image'
import PageAudio from 'components/doc/audio'
import { K_PAD_DOCUMENT, K_PAD_NEW_QUESTION, K_PAD_PAPER, K_PAD_EXAM_PAPER, K_NEW_QUESTION_BANK_PAPER } from 'assets/js/consts/common'
import NewQuestion from 'components/new-question'
import Paper from 'components/paper'
import NewPaper from 'components/new-paper'

export default {
  name: 'resource-preview',
  props: {
    previewItem: { required: true },
  },
  data() {
    return {
    }
  },
  components: {
    Modal, PageModal, PageDocument, PageImage, PageAudio, NewQuestion, Paper, NewPaper,
  },
  computed: {
    // modal-video 单独处理
    previewItemView() {
      if (!this.previewItem) {
        return null
      }
      if (this.previewItem.kind === K_PAD_DOCUMENT) {
        const props = { fileUrl: this.previewItem.fileUrl }
        switch (this.previewItem.fileType) {
          case 2020:
          case 2024:
          case 2028:
          case 2021:
            return { type: 'page-document', props }
          case 2022:
            return { type: 'page-image', props }
          case 2023:
            return { type: 'page-audio', props }
          default:
            return null
        }
      } else if (this.previewItem.kind === K_PAD_EXAM_PAPER) {
        const p = { fileUrl: this.previewItem.fileUrl }
        return { type: 'page-document', props: p }
      } else if (this.previewItem.kind === K_PAD_NEW_QUESTION) {
        return { type: 'new-question', props: { nq: this.previewItem } }
      } else if (this.previewItem.kind === K_PAD_PAPER) {
        return { type: 'paper', props: { paper: this.previewItem } }
      } else if (this.previewItem.kind === K_NEW_QUESTION_BANK_PAPER) {
        return { type: 'new-paper', props: { paper: this.previewItem } }
      }
    },
  },
  methods: {
    hasQuestions(item) {
      return item && [K_PAD_NEW_QUESTION, K_PAD_PAPER, K_NEW_QUESTION_BANK_PAPER].includes(item.kind)
    },
    isDocument(item) {
      if (item) {
        return [K_PAD_DOCUMENT, K_PAD_EXAM_PAPER].includes(item.kind)
      }
    },
    isExamPaper(item) {
      return item && item.kind === K_PAD_EXAM_PAPER
    },
    modalSizeByDoc(item) {
      if (item && item.fileType === 2023) {
        return 'modal-small'
      }
      return 'modal-large'
    },
  },
}
</script>
<style lang="less">
@import "~assets/css/base-variables.less";
.page-modal.modal-large.page-document .modal-container {
  height: 80%;
}
.page-modal.modal-small.page-document .modal-container {
  // audio
  .doc-file {
    min-height: 200px;
    height: 250px;
  }
}
.page-modal.page-document .modal-container {
  display: flex;
  flex-direction: column;
  background-color: white;

  .doc-title {
    font-size: 1.1em;
    padding: .5em 1em;
    margin: 0;
    font-weight: normal;
    background-color: #f4f4f4;
  }
  .doc-file {
    flex: 1;
    height: 100%;
  }
  .file-type-2021.doc-title, .file-type-2020.doc-title {
    background-color: #2E5796;
    color: white;
  }
  .file-type-2024.doc-title {
    background-color: #D04D38;
    color: white;
  }
  .file-type-2028.doc-title {
    background-color: #393B3E;
    color: white;
  }

  // image
  .file-type-2022.doc-title {
    border-bottom: solid 1px #ddd;
    background-color: #f4f4f4;
  }
  .viewer-container {
    background-color: #f0f0f0;
  }
}

.page-modal.page-video .modal-container {
  box-shadow: none;
  border-radius: 0;
  width: fit-content;
  height: fit-content;
  background-color: transparent;
  max-width: 90vw;
  max-height: 80vh;
  min-width: unset;

  position: relative;
  padding: 0;

  .inbody-title {
    font-size: 16px;
    color: white;
    position: absolute;
    padding-bottom: .5em;
    padding-left: 1rem;
    padding-top: .2rem;
    text-align: center;
    background: linear-gradient(rgba(0,0,0,.7), rgba(0,0,0,.5), rgba(0,0,0,.4), rgba(0,0,0,.05));
    left: 0;
    right: 0;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
  }

  video {
    max-width: 90vw;
    max-height: 80vh;
  }
}
</style>



