import _ from 'lodash'
import parser from './richtext'

export class Audio {
  constructor({ id, textHtml, fileUrl }) {
    this.id = id
    this.textHtml = textHtml
    this.fileUrl = fileUrl
  }
  static extractParams(sourceData) {
    return {
      id: sourceData.id,
      textHtml: parser(sourceData.audio_text),
      fileUrl: sourceData.audio_path,
    }
  }
}

export class Knowledge {
  constructor({ id, name }) {
    this.id = id
    this.name = name
  }
  static extractParams(sourceData) {
    return {
      id: sourceData.id,
      name: sourceData.name,
      isValid: sourceData.name ? true : false,
    }
  }
}


export class HtmlAnswer {
  constructor({ id, textHtml }) {
    this.id = id
    this.textHtml = textHtml
  }
  static extractParams(sourceData) {
    return {
      id: sourceData.id,
      textHtml: parser(sourceData.answer_text),
    }
  }
}


export class TextAnswer {
  constructor({ id, text }) {
    this.id = id
    this.text = text
  }
  static extractParams(sourceData) {
    return {
      id: sourceData.id,
      text: sourceData.answer_text,
    }
  }
}

export class Option {
  constructor({ id, optionHtml, seqId, isAnswer = false }) {
    this.id = id
    this.optionHtml = optionHtml
    this.seqId = seqId
    this.seqLabel = String.fromCharCode(64 + seqId)
    this.isAnswer = isAnswer
    this.hasReport = false
  }

  static extractParams(sourceData) {
    return {
      id: sourceData.id,
      optionHtml: parser(sourceData.option_text),
      seqId: sourceData.seq,
      isAnswer: sourceData.is_answer || false,
    }
  }

  injectReportData(data) {
    this.sids = data[this.seqId] || []
    this.hasReport = true
  }

  isUserAnswer(sid) {
    if (this.hasReport) {
      return this.sids.indexOf(sid) >= 0
    }
  }
}


export class ChoiceSubQuestionNoStem {
  constructor({ id, kind, seqId, answer, analysisHtml, optionData, knowledgeData }) {
    this.id = id
    this.kind = kind
    this.seqId = seqId
    this.analysisHtml = analysisHtml

    this.answer = answer
    this.options = []
    for (const data of optionData) {
      const params = Option.extractParams(data)
      const option = new Option(params)
      if (this.answer && option.seqId === this.answer) {
        option.isAnswer = true
      }
      if (!this.answer && option.isAnswer) {
        this.answer = option.seqId
      }
      this.options.push(option)
    }
    this.options.sort((a, b) => a.seqId - b.seqId)
    this.answerLabel = String.fromCharCode(64 + this.answer)

    this.knowledges = []
    for (const data of knowledgeData) {
      const params = Knowledge.extractParams(data)
      if (params.isValid) {
        this.knowledges.push(new Knowledge(params))
      }
    }

    this.hasReport = false
  }

  static extractParams(sourceData) {
    return {
      id: sourceData.id,
      kind: sourceData.kind,
      seqId: sourceData.seq_id,
      answer: sourceData.answer,
      analysisHtml: parser(sourceData.analysis),
      optionData: sourceData.options,
      knowledgeData: sourceData.knowledges,
    }
  }

  injectReportData(data) {
    for (const option of this.options) {
      option.injectReportData(data.answerResults)
    }
    this.unansweredSids = data.unansweredSids || []
    this.hasReport = true
  }
}


export class TextSubQuestion {
  constructor({ id, seqId, stemHtml, answerData, analysisHtml, knowledgeData }) {
    this.id = id
    this.seqId = seqId
    this.stemHtml = stemHtml
    this.analysisHtml = analysisHtml

    this.answers = []
    for (const data of answerData) {
      const params = HtmlAnswer.extractParams(data)
      this.answers.push(new HtmlAnswer(params))
    }

    this.knowledges = []
    for (const data of knowledgeData) {
      const params = Knowledge.extractParams(data)
      if (params.isValid) {
        this.knowledges.push(new Knowledge(params))
      }
    }
  }

  static extractParams(sourceData) {
    return {
      id: sourceData.id,
      seqId: sourceData.seq_id,
      stemHtml: parser(sourceData.question_text),
      answerData: sourceData.answers,
      analysisHtml: parser(sourceData.analysis),
      knowledgeData: sourceData.knowledges,
    }
  }
}


// 解题方法
export class Method {
  constructor({ id, name }) {
    this.id = id
    this.name = name
  }

  static extractParams(sourceData) {
    return {
      id: sourceData.id,
      name: sourceData.name,
      isValid: sourceData.name ? true : false,
    }
  }
}


// 数学解析
export class Analysis {
  constructor({ id, textHtml, knowledgeData, methodData }) {
    this.id = id
    this.textHtml = textHtml
    this.knowledges = []
    this.methods = []

    this.isEmpty = textHtml ? false : true
    for (const data of knowledgeData) {
      const params = Knowledge.extractParams(data)
      if (params.isValid) {
        this.isEmpty = false
        this.knowledges.push(new Knowledge(params))
      }
    }
    for (const data of methodData) {
      const params = Method.extractParams(data)
      if (params.isValid) {
        this.isEmpty = false
        this.methods.push(new Method(params))
      }
    }
  }

  static extractParams(sourceData) {
    return {
      id: sourceData.id,
      textHtml: parser(sourceData.analysis_text),
      knowledgeData: sourceData.knowledges,
      methodData: sourceData.methods,
    }
  }
}


// 数学解法
export class Solution {
  constructor({ id, textHtml, analysisData }) {
    this.id = id
    this.textHtml = textHtml

    this.analyses = []
    this.methods = []
    this.knowledges = []

    this.isEmpty = textHtml ? false : true
    for (const data of analysisData) {
      const params = Analysis.extractParams(data)
      const analysis = new Analysis(params)
      if (!analysis.isEmpty) {
        if (analysis.methods.length) {
          this.methods = _.union(this.methods, analysis.methods)
        }
        if (analysis.knowledges.length) {
          this.knowledges = _.union(this.knowledges, analysis.knowledges)
        }
        this.analyses.push(new Analysis(params))
        this.isEmpty = false
      }
    }
  }
  static extractParams(sourceData) {
    return {
      id: sourceData.id,
      textHtml: parser(sourceData.solution_text),
      analysisData: sourceData.analyses,
    }
  }
}


export class MathSubQuestion {
  constructor({ id, seqId, stemHtml, solutionData, answerData }) {
    this.id = id
    this.seqId = seqId
    this.stemHtml = stemHtml
    this.solutions = []
    for (const data of solutionData) {
      const params = Solution.extractParams(data)
      const solution = new Solution(params)
      if (!solution.isEmpty) {
        this.solutions.push(new Solution(params))
      }
    }

    this.answers = []
    for (const data of answerData) {
      const params = HtmlAnswer.extractParams(data)
      this.answers.push(new HtmlAnswer(params))
    }
  }

  static extractParams(sourceData) {
    return {
      id: sourceData.id,
      seqId: sourceData.seq_id,
      answer: sourceData.answer,
      stemHtml: parser(sourceData.question_text || []),
      solutionData: sourceData.solutions,
      answerData: sourceData.answers,
    }
  }
}



