
export default class NqSubjective {
  constructor({ id, seqId, kind, note, score }) {
    this.id = id
    this.seqId = seqId
    this.kind = kind
    this.kindName = '主观题'
    this.key = `key-${kind}-${id}`
    this.note = note
    this.score = score

    this.isNq = true
    this.isSubjective = true

    this.hasReport = false
    this.unansweredSids = []
    this.results = []
  }

  static extractParams(sourceData) {
    return {
      id: sourceData.seqId,
      seqId: sourceData.seqId,
      kind: sourceData.kind,
      note: sourceData.note,
      score: sourceData.score,
    }
  }

  injectReportData(data) {
    if (data.kind !== this.kind || data.seqId !== this.seqId) {
      return
    }

    this.unansweredSids = data.unansweredSids
    this.results = data.results
    this.hasReport = true
  }
}





