<template>
  <div id="vote-list">
    <div class="menu-list">
      <button class="ui green button pull-right" @click="showAddVote=true">创建投票</button>
    </div>
    <table class="ui celled table">
      <thead>
        <tr>
          <th>ID</th>
          <th>标题</th>
          <th>班级</th>
          <th>创建时间</th>
          <th>操作</th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="vote in votes" class="image" v-bind:key="vote.id">
          <td>{{ vote.id }}</td>
          <td>{{ vote.title }}</td>
          <td>{{ vote.course.name }}</td>
          <td>{{ new Date(vote.creationTime).toLocaleString('zh-CN') }}</td>
          <td>
            <button class="ui icon circular button" @click="previewItem=vote">查看</button>
            <button class="ui icon circular button" @click="deleteItem=vote">删除</button>
          </td>
        </tr>
      </tbody>
    </table>
    <infinite-loading
      force-use-infinite-wrapper="true"
      spinner="waveDots"
      @infinite="infiniteHandler">
        <span slot="no-more"></span>
        <span slot="no-results">还没有投票呢 :(</span>
    </infinite-loading>

    <modal v-if="previewItem" class="modal-large item-preview" distance="10"
      @close="previewItem=null"
      :withFooter="false">
      <h3 slot="header"><span>{{ previewItem.title }} - 投票详情</span></h3>
      <template slot="body">
        <div>
          <span v-if="previewItem.isAnonymous">匿名投票</span>
          <span v-else>实名投票</span>
          &nbsp;|&nbsp;
          <span v-if="previewItem.isSingleChoice">单选</span>
          <span v-else>多选</span>
          &nbsp;|&nbsp;
          <span>共 {{ previewItem.totalVote }} 票</span>
          &nbsp;|&nbsp;
          截止投票时间：<span>{{ new Date(previewItem.deadline).toLocaleString('zh-CN') }}</span>

          <span class="pull-right">
            <input type="checkbox" v-model="sortOption"/>按票数排序&nbsp;&nbsp;
          </span>
        </div>

        <table class="ui striped table">
          <tbody>
            <tr v-for="option in previewItem.options" class="image" v-bind:key="option.id">
              <td>{{ option.text }}</td>
              <td>{{ option.vote }} 票</td>
              <td>{{ percentage(option.vote, previewItem.totalVote) }}</td>
              <td v-if="!previewItem.isAnonymous">
                <span v-for="(stu, index) in option.users" v-bind:key="stu.id">
                  <span>{{ stu.name }}</span>
                  <span v-if="index + 1< option.users.length">、</span>
                </span>
                <span v-if="option.vote > 5">等</span>
              </td>
            </tr>
          </tbody>
        </table>

      </template>
    </modal>

    <modal v-if="deleteItem" class="item-delete" :withHeader="false" @close="deleteItem=null">
      <div slot="body">
        <span class="ui text-center circular label">
          <i class="ui red warning icon"></i>
        </span>
        <p class="alert-text">确认要删除这个投票？</p>
      </div>
      <div slot="footer">
        <button class="ui basic button" @click="deleteItem=null">取消</button>
        <button class="ui red button" @click="actionDelItem()">删除</button>
      </div>
    </modal>

    <modal v-if="showBatchAddOption" :withHeader="false" @close="closeBatchAddOptionModal">
      <div slot="body">
        <textarea v-model='optionText' placeholder="每行输入一个选项，最多 100 个选项" rows="10" cols="60"></textarea>
      </div>
      <div slot="footer">
        <button class="ui basic button" @click="closeBatchAddOptionModal">取消</button>
        <button class="ui red button" @click="batchAddOption">确定</button>
      </div>
    </modal>

    <modal v-if="showAddVote" class="modal-large" distance="10"
      @close="showAddVote=false"
      :withFooter="true">
      <h3 slot="header"><span>创建投票</span></h3>
      <template slot="body">
        <form class="ui form">

          <div class="twelve wide field">
            <input type="text" v-model="title" placeholder="投票标题" maxlength="100"/>
          </div>

          <div class="field inline">
            <label>班级</label>
            <select v-model="course" class="ui fluid dropdown">
              <option v-for="course in teacher.courses" :value="course.id" v-bind:key="course.id">{{ course.name }}</option>
            </select>
          </div>

          <div v-for="(option, index) in options" class="field inline" v-bind:key="option.id">
            <i class="minus circle icon red" @click="removeOption(index)"></i>
            <input type="text" placeholder="选项" v-model="options[index]" maxlength="100"/>
          </div>

          <div class="field">
            <i class="plus circle icon blue" @click="addOption"></i>
            <a href="#" class="add-option" @click="addOption">&nbsp;&nbsp;添加选项</a>
            &nbsp;&nbsp;
            <i class="plus circle icon blue" @click="addOption"></i>
            <a href="#" class="add-option" @click="showBatchAddOptionModal">批量添加选项</a>
          </div>

          <div class="inline fields">
            <div class="field radio checkbox">
              <input type="radio" name="isSingle" :checked="isSingleChoice==='1'" value="1" v-model="isSingleChoice"/>
              <label>单选</label>
            </div>
            <div class="field radio checkbox">
              <input type="radio" name="isSingle" value="0" v-model="isSingleChoice" />
              <label>多选</label>
            </div>
          </div>

          <div class="fields inline">
            <div class="ui field">
              <label>最少选几项</label>
              <input type="number" v-model="minOption" min="1" max="100" :disabled="isSingleChoice==='1'" />
            </div>

            <div class="ui field">
              <label>最多选几项</label>
              <input type="number" v-model="maxOption" min="1" max="100" :disabled="isSingleChoice==='1'"/>
            </div>
          </div>

          <div class="field inline">
            <label>截止日期</label>
            <time-picker
              place-holder-text="默认三天后"
              v-model="deadline"
              :minDate="minDate"
              :maxDate="maxDate"
              class="setting-content"></time-picker>
          </div>

          <div class="field">
            <div class="ui checkbox">
              <input type="checkbox" :checked="isAnonymous" v-model="isAnonymous"/>
              <label>匿名投票</label>
            </div>
          </div>

        </form>
      </template>

      <div slot="footer">
        <button class="ui basic button" @click="showAddVote=false">取消</button>
        <button class="ui green button" @click="addVote">添加</button>
      </div>
    </modal>

  </div>
</template>

<script>
import axios from 'axios'
import { mapState } from 'vuex'
import PageImage from 'components/doc/image'
import Modal from 'components/modal'
import Icon from 'components/icons/Icon'
import 'components/icons/slide-show'
import 'components/icons/remove'

const qs = require('qs')

export default {
  name: 'vote-list',
  props: {},
  data() {
    return {
      sortOption: false,
      optionText: '',
      showBatchAddOption: false,
      loading: false,
      votes: [],
      start: 0,
      course: 0,
      previewItem: null,
      deleteItem: null,
      showAddVote: false,
      title: '',
      options: ['', ''],
      isAnonymous: false,
      isSingleChoice: '1',
      deadline: null,
      minOption: 1,
      maxOption: 1,
      maximumOptions: 100,
    }
  },
  components: {
    InfiniteLoading: () => import(/* webpackChunkName: 'vue-infinite-loading' */ 'vue-infinite-loading'),
    Icon,
    PageImage,
    Modal,
    TimePicker: () => import('components/time-picker'),
  },
  computed: {
    minDate: () => new Date(),
    maxDate: () => {
      let max = new Date()
      max.setDate(max.getDate() + 15)
      return max
    },
    ...mapState(['teacher']),
  },
  mounted() {
  },
  watch: {
    sortOption () {
      if (!this.previewItem) {
        return
      }
      if (this.sortOption) {
        this.previewItem.options.sort(function(a, b){
          return b.vote - a.vote
        })
      } else {
        this.previewItem.options.sort(function(a, b){
          return a.id - b.id
        })
      }
    }
  },
  methods: {
    showBatchAddOptionModal() {
      this.showAddVote = false
      this.showBatchAddOption = true
    },
    closeBatchAddOptionModal() {
      this.showAddVote = true
      this.showBatchAddOption = false
    },
    batchAddOption() {
      const options = this.optionText.split('\n')
      for (let text of options) {
        if (this.options.length >= this.maximumOptions) {
          break
        }
        this.options.push(text)
      }
      this.closeBatchAddOptionModal()
    },
    addVote() {
      if (this.title.length === 0) {
        this.$toasted.error('标题不能为空', { duration: 2000, position: 'top-center' })
        return
      }
      if (this.options.length < 2) {
        this.$toasted.error('至少要有两个选项', { duration: 2000, position: 'top-center' })
        return
      }
      if (!this.course) {
        this.$toasted.error('请选择班级', { duration: 2000, position: 'top-center' })
        return
      }
      if (this.isSingleChoice === '0') {
        this.minOption = parseInt(this.minOption)
        this.maxOption = parseInt(this.maxOption)
        if (this.minOption > this.options.length) {
          this.$toasted.error('最多选几项必须大于等于最少选几项', { duration: 2000, position: 'top-center' })
          return
        }
        if (this.minOption >= this.options.length) {
          this.$toasted.error('选项个数必须大于最少选几项', { duration: 2000, position: 'top-center' })
          return
        }
        if (this.maxOption > this.options.length) {
          this.$toasted.error('选项个数必须大于等于最多选几项', { duration: 2000, position: 'top-center' })
          return
        }
      }

      const params = {
        course: this.course,
        title: this.title,
        options: this.options,
        isAnonymous: this.isAnonymous + 0,
        deadline: this.deadline && new Date(this.deadline),
        isSingleChoice: this.isSingleChoice,
      }
      if (this.isSingleChoice === '0') {
        params.maxOption = this.maxOption
        params.minOption = this.minOption
      }
      axios.post('/pad/j/votes/', qs.stringify(params, { arrayFormat: 'repeat' })).then((response) => {
        if (response.data.status === 0) {
          this.$toasted.success('创建成功', { duration: 2000, position: 'top-center' })
          this.showAddVote = false
          const v = response.data.vote
          this.votes.unshift(v)
        } else {
          this.$toasted.error(response.data.detail, { duration: 2000, position: 'top-center' })
        }
      })
    },
    actionDelItem() {
      if (!this.deleteItem) return

      axios.delete(`/pad/j/votes/${this.deleteItem.id}`).then((response) => {
        if (response.data.status === 0) {
          const index = this.votes.indexOf(this.deleteItem)
          this.votes.splice(index, 1)
          this.deleteItem = null
          this.$toasted.error('删除成功', { duration: 800, position: 'top-center' })
        } else if (response.data.status === 1) {
          this.$toasted.error(response.data.detail, { duration: 800, position: 'top-center' })
        }
      })
    },
    percentage(value, base) {
      if (!value) {
        return '-'
      }
      if (!base) {
        return '-'
      }
      const p = Math.floor((value / base) * 100)
      return `${p}%`
    },
    infiniteHandler($state) {
      const params = { start: this.start, limit: 10 }
      axios.get('/pad/j/votes/mine', { params }).then((response) => {
        if (response.data.status === 0) {
          const votes = response.data.votes
          if (votes.length === 0) {
            $state.complete()
          } else {
            this.start += votes.length
            this.votes = this.votes.concat(votes)
            $state.loaded()
          }
        }
      })
    },
    addOption() {
      if (this.options.length >= this.maximumOptions) {
        return
      }
      this.options.push('')
    },
    removeOption(index) {
      this.options.splice(index, 1)
    },
  },
}
</script>

<style lang="less">
@import "~assets/css/base-variables.less";
@import "/static/w/assets/semantic/semantic.min.css";

.menu-list {
  padding-bottom: 25px;
}

.modal-header h3 span {
  font-size: 1rem;
}

.votes-list {
  min-height: 100vh;
}

.add-option {
  text-decoration: none;
  color: blue;
}

</style>



