<template>
  <div class="courseware-panel" id="courseware-panel">
    <div class="courseware-container" id="courseware-container">
      <div class="container-heading">
        <img :src="require('assets/img/icon-cw.png')">
        <h2 class="container-title" v-if="courseware">课件包：{{courseware.name}}</h2>
      </div>
      <div class="ui secondary pointing menu">
        <a class="item" @click="curTab=1" :class="{active: curTab === 1}">
          课件包详情
        </a>
        <a class="item" @click="curTab=2" :class="{active: curTab === 2}">
          高级 / 批量发送
        </a>
        <div class="right menu">
          <a class="ui info item">
            说明
          </a>
          <div class="ui info popup transition hidden">
            <ul>
              <li class="note">课件包中的作业只能在班级层面发送，无法按学生/按组发送。</li>
              <li class="note">所有发送相关的设置中：只显示可发送的文件，或可发送的班级，已发送的情况会被隐藏。</li>
            </ul>
          </div>
        </div>
      </div>
      <div v-if="courseware">
        <div class="basic-info" v-show="curTab === 1">
          <button class="ui icon button right floated play-cw" @click="playCw(courseware)">
            <icon name="slide-show"></icon>
            <span>播放课件包</span>
          </button>
          <div>
            <span class="item-label">课件包名称：</span>
            <span>{{courseware.name}}</span>
          </div>
          <div>
            <span class="item-label">创建时间：</span>
            <span>{{courseware.createdTime}}</span>
          </div>
          <div>
            <span class="item-label">关联章节：</span>
            <span>{{displayChapter}}</span>
          </div>
          <button class="link link-primary" @click="openBasicsEditor=true">编辑基础信息&gt;&gt;</button>
        </div>
        <div class="table-wrapper" v-show="curTab === 1">
          <table class="ui small celled table">
            <thead>
              <tr>
                <th class="top aligned">名称<br><span class="note">点击预览</span></th>
                <th class="top aligned">
                  <span class="send-status-label"><span class="status-dot teal-dot"></span>已收卷/已完成<br><span class="note">点击标签跳转到作业</span></span>
                </th>
                <th class="top aligned">
                  <span class="send-status-label"><span class="status-dot orange-dot"></span>学生作答中<br><span class="note">点击标签跳转到作业</span></span>
                </th>
                <th class="top aligned">
                  <span class="send-status-label"><span class="status-dot gray-dot"></span>定时中<br><span class="note">待发送</span></span>
                </th>
                <th class="top aligned">
                  <span class="send-status-label"><span class="status-dot gray-dot"></span>未发送<br><span class="note">点击标签发送作业</span></span>
                </th>
              </tr>
            </thead>
            <tbody>
              <tr v-for="page of pages">
                <td class="page-title" @click="previewItem=page.target">
                  <i class="pad icon" :class="'ft-'+page.target.kind" v-if="page.target.kind===2025"></i>
                  <i class="pad icon" :class="'ft-'+page.target.fileType" v-else></i>
                  <p>{{page.target.name}} <span class="note">{{classKindMap[page.classKind]}}</span></p>
                </td>
                <td class="courses-holder">
                  <span v-for="cm of page.courseMaterials" v-if="cm.status === statusReviewed || (!cm.needSubmit && cm.status!==statusTimed)" class="course-label" @click="openCm(cm.id)">{{cm.courseName}}</span>
                </td>
                <td class="courses-holder">
                  <span v-for="cm of page.courseMaterials" v-if="cm.needSubmit && cm.status === statusTodo" class="course-label" @click="openCm(cm.id)">{{cm.courseName}}</span>
                </td>
                <td class="courses-holder">
                  <span v-for="cm of page.courseMaterials" v-if="cm.status === statusTimed" class="course-label disabled">{{cm.courseName}}</span>
                </td>
                <td class="courses-holder">
                  <span v-for="courseId of page.unsendCourseIds" class="course-label" @click="sendFile(page, [courseId])">{{courseIdMap[courseId].name}}</span>
                  <button class="link send link-primary"
                    @click="sendFile(page, page.unsendCourseIds)"
                    v-if="page.unsendCourseIds.length">统一发送</button>
                </td>
              </tr>
            </tbody>
          </table>
        </div>
        <div class="advanced-send" v-show="curTab === 2">
          <div class="send-config ui form">
            <div class="send-header">
              <div class="ui fitted checkbox">
                <input type="checkbox" :checked="allItemsToggleStatus" @change="toggleAllItems">
                <label></label>
              </div>
              <div class="item-name"> <span>可发送的文件</span><br><span class="note">输入修改发给学生的作业名</span></div>
              <div class="item-setting">
                <span>详情 <span class="link link-primary" @click="openGlobalConfig=true">(全局统一设置)</span><br><span class="note">可单独设置</span></span>
              </div>
            </div>
            <div class="send-body">
              <div class="send-item" v-for="sItem of gSendItems">
                <div class="ui fitted checkbox">
                  <input type="checkbox" :checked="sItem.switchOn" @change="toggleItem(sItem)">
                  <label></label>
                </div>
                <div class="item-name">
                  <div class="ui mini input item-name">
                    <input type="text" v-model="sItem.cmTitle" placeholder="不超过100个字符">
                  </div>
                  <div class="item-type" v-if="sItem.switchOn">
                    <span class="note">{{classKindMap[sItem.page.classKind]}} / </span>
                    <span class="note">{{resourceTypeNameMap[sItem.page.target.fileType]}} /</span>
                    <span class="link link-default" @click="previewItem=sItem.page.target">预览</span>
                  </div>
                </div>
                <div class="item-type" v-if="!sItem.switchOn">
                  <span class="note">{{classKindMap[sItem.page.classKind]}} / </span>
                  <span class="note">{{resourceTypeNameMap[sItem.page.target.fileType]}} /</span>
                  <span class="link link-default" @click="previewItem=sItem.page.target">预览</span>
                </div>
                <div class="item-setting-detail" v-else>
                  <div class="setting-row">
                    <span class="setting-detail-label">发送给: </span>
                    <div class="setting-detail-content">
                      <div class="ui checkbox" v-for="cId of sItem.page.unsendCourseIds">
                        <input type="checkbox" tabindex="0" class="hidden" :id="sItem.page.id + '-course-' + cId"
                          :value="cId" v-model="sItem.sendCourseIds">
                        <label :for="sItem.page.id + '-course-' + cId">{{courseIdMap[cId].name}}</label>
                      </div>
                    </div>
                  </div>
                  <div class="setting-row">
                    <span class="setting-detail-label">发布时间: </span>
                    <div class="setting-detail-content">
                      <time-picker
                        place-holder-text="立即发送"
                        :key="sItem.page.id + '-publishtime-picker'"
                        :minDate="minDate"
                        :maxDate="maxDate"
                        v-model="sItem.publishTime"></time-picker>
                    </div>
                  </div>
                  <div class="setting-row" v-if="sItem.needSubmit">
                    <span class="setting-detail-label">定时收卷: </span>
                    <div class="setting-detail-content">
                      <time-picker place-holder-text="未设置（可不填）"
                        :key="sItem.page.id + '-deadline-picker'"
                        :minDate="minDate"
                        :maxDate="maxDate"
                        :initOffsetMinutes="10"
                        v-model="sItem.deadline"></time-picker>
                    </div>
                  </div>
                  <div class="setting-row" v-if="sItem.needSubmit">
                    <span class="setting-detail-label">开放答案：</span>
                    <div class="setting-detail-content">
                      <div class="ui checkbox">
                        <input type="checkbox" tabindex="0" class="hidden" :id="sItem.page.id + '-open-answer'"
                          v-model="sItem.openAnswer">
                        <label :for="sItem.page.id + '-open-answer'">开放</label>
                      </div>
                    </div>
                  </div>
                  <div class="setting-row">
                    <span class="setting-detail-label">备注: </span>
                    <div class="setting-detail-content">
                      <input type="text" v-model="sItem.note" placeholder="学生可见（选填），不超过100个字符。">
                    </div>
                  </div>

                </div>
              </div>
            </div>
          </div>
          <button class="ui small primary button" :class="canGlobalSend?'':'disabled'"
            @click="sendFileToCourses">发送文件</button> <span class="note">勾选文件并选择班级并发送</span>
        </div>
      </div>
    </div>
    <resource-preview
      :previewItem="previewItem"
      @close="previewItem=null"></resource-preview>
    <modal v-if="openGlobalConfig && courseware" class="modal-large" @close="openGlobalConfig=false" :withFooter="false">
      <h3 slot="header">文件批量发送的全局设置</h3>
      <div slot="body">
        <form class="courseware-form ui form">
          <div class="g-config-item">
            <div class="g-config-item-label">发送给</div>
            <div class="g-config-item-content">
              <div class="ui checkbox" v-for="course, cId in courseIdMap">
                <input type="checkbox" tabindex="0" class="hidden" :id="'g-course-' + cId"
                  :value="course.id" v-model="globalConfig.sendCourseIds">
                <label :for="'g-course-' + cId">{{course.name}}</label>
              </div>
            </div>
            <div class="g-config-item-action"><span class="link link-primary" @click="globalApply('course')">全局应用</span></div>
          </div>
          <div class="g-config-item">
            <div class="g-config-item-label">发布时间</div>
            <div class="g-config-item-content">
              <time-picker
                place-holder-text="立即发送"
                key="gconfig + '-publishtime-picker'"
                :minDate="minDate"
                :maxDate="maxDate"
                v-model="globalConfig.publishTime"></time-picker>
            </div>
            <div class="g-config-item-action"><span class="link link-primary" @click="globalApply('publishTime')">全局应用</span></div>
          </div>
          <div class="g-config-item">
            <div class="g-config-item-label">定时收卷</div>
            <div class="g-config-item-content">
              <time-picker
                place-holder-text="未设置（可不填）"
                key="gconfig + '-deadline-picker'"
                :initOffsetMinutes="10"
                :minDate="minDate"
                :maxDate="maxDate"
                v-model="globalConfig.deadline"></time-picker>
            </div>
            <div class="g-config-item-action"><span class="link link-primary" @click="globalApply('deadline')">全局应用</span></div>
          </div>
          <div class="g-config-item">
            <div class="g-config-item-label">开放答案</div>
            <div class="g-config-item-content">
              <div class="ui checkbox">
                <input type="checkbox" tabindex="0" class="hidden" id="g-open-answer" v-model="globalConfig.openAnswer">
                <label for="g-open-answer">开放</label>
              </div>
            </div>
            <div class="g-config-item-action"><span class="link link-primary" @click="globalApply('openAnswer')">全局应用</span></div>
          </div>
          <div class="g-config-item">
            <div class="g-config-item-label">备注</div>
            <div class="g-config-item-content">
              <input type="text" v-model="globalConfig.note" placeholder="学生可见（选填）">
            </div>
            <div class="g-config-item-action"><span class="link link-primary" @click="globalApply('note')">全局应用</span></div>
          </div>
          <ul>
            <li class="note">针对该课件包内所有可发送文件，点击一项设置的「全局应用」会覆盖所有可发送文件中这项设置的值。</li>
            <li class="note">这里列出你任课的所有教学班，「全局应用」的时候会过滤掉一个文件已发送的班级。</li>
            <li class="note">收卷时间和是否开放答案只会应用在试题组和互动资料上。</li>
          </ul>
        </form>
      </div>
    </modal>
    <modal v-if="openBasicsEditor && courseware" @close="openBasicsEditor=false">
      <h3 slot="header">编辑课件包基础信息</h3>
      <div slot="body">
        <form class="courseware-form ui form">
          <div class="field">
            <label>名称*</label>
            <input type="text" v-model="cwName" placeholder="不超过100个字符">
          </div>
          <div class="field text-muted">
            章节修改功能即将上线。
          </div>
        </form>
      </div>
      <div slot="footer">
        <button class="ui tiny blue button" @click="editCourseware" :class="canPostEdit?'':'disabled'">保存</button>
      </div>
    </modal>
    <file-sender
      @close="closeSingleSender"
      @success="refetchAndGotoTab"
      :page="singleSendPage"
      :courses="singleSendCourses"
      :courseware="courseware"
      v-if="singleSendPage && singleSendCourses.length && courseware"></file-sender>
  </div>
</template>

<script>
import { mapState } from 'vuex'
import axios from 'axios'
import _ from 'lodash'
import Modal from 'components/modal'
import TimePicker from 'components/time-picker'
import 'components/icons/slide-show'
import Icon from 'components/icons/Icon'
import ResourcePreview from 'components/resource-preview'
import {
  BEFORE_CLASS, IN_CLASS, AFTER_CLASS, RESOURCE_TYPE_NAME_MAP,
  CM_STATUS_TODO, CM_STATUS_REVIEWED, CM_STATUS_TIMED,
} from 'assets/js/consts/common'
import FileSender from './file-sender'

const qs = require('qs')

export default {
  name: 'courseware-detail',
  data() {
    return {
      classKindMap: {
        [BEFORE_CLASS]: '课前',
        [IN_CLASS]: '课中',
        [AFTER_CLASS]: '课后',
      },
      courseware: null,
      statusTodo: CM_STATUS_TODO,
      statusReviewed: CM_STATUS_REVIEWED,
      statusTimed: CM_STATUS_TIMED,
      resourceTypeNameMap: RESOURCE_TYPE_NAME_MAP,
      previewItem: null,
      openBasicsEditor: false,
      cwName: '',
      singleSendPage: null,
      singleSendCourses: [],
      openFileSender: false,
      curTab: 1,
      gSendItems: null,
      openGlobalConfig: false,
      globalConfig: {
        publishTime: null,
        deadline: null,
        openAnswer: false,
        sendCourseIds: [],
        note: '',
      },
    }
  },
  mounted() {
    this.fetchCoursewareData()
    $('.ui.info.item').popup({
      popup: $('.ui.info.popup'),
      on: 'click',
      position: 'left center',
    })
  },
  beforeDestroy() {
    $('.ui.info.button').popup('destroy')
  },
  components: {
    Modal, TimePicker, FileSender, Icon, ResourcePreview,
  },
  watch: {
    '$route.params.cwId': function (val) {
      this.fetchCoursewareData(val)
    },
    openBasicsEditor(val, oldVal) {
      if (oldVal && !val) {
        this.$nextTick().then(() => {
          this.cwName = this.courseware.name
        })
      }
    },
  },
  computed: {
    allItemsToggleStatus() {
      for (const item of this.gSendItems) {
        if (!item.switchOn) {
          return false
        }
      }
      return true
    },
    canGlobalSend() {
      for (const item of this.gSendItems) {
        if (item.sendCourseIds.length) {
          return true
        }
      }
      return false
    },
    canPostEdit() {
      if (this.courseware) {
        return this.courseware.name !== this.cwName && this.cwName.length > 0
      }
    },
    displayChapter() {
      if (!this.courseware) {
        return ''
      }
      if (this.courseware.chapter) {
        return this.courseware.chapter.label
      }
      return '无'
    },
    pages() {
      if (!this.courseware) return []
      return _.concat(this.courseware.pages[3], this.courseware.pages[2], this.courseware.pages[1])
    },
    minDate: () => new Date(),
    maxDate: () => {
      let max = new Date()
      max.setDate(max.getDate() + 15)
      return max
    },
    ...mapState(['courses', 'courseIdMap']),
  },
  methods: {
    playCw(cw) {
      window.open(`/pad/coursewares/${cw.id}/detail`, '_blank')
    },
    closeSingleSender() {
      this.singleSendPage = null
      this.singleSendCourses = []
    },
    toggleAllItems() {
      if (this.allItemsToggleStatus) {
        for (const item of this.gSendItems) {
          if (item.switchOn) {
            item.switchOn = false
          }
        }
      } else  {
        for (const item of this.gSendItems) {
          if (!item.switchOn) {
            item.switchOn = true
          }
        }
      }
    },
    toggleItem(sItem) {
      sItem.switchOn = !sItem.switchOn
    },
    openCm(cmId) {
      window.open(`/pad/course-material/${cmId}`, '_blank')
    },
    editCourseware() {
      if (!this.cwName || this.cwName.length >= 100) {
        this.$toasted.error('课件包名称必填且不能超过100个字符', { duration: 1000, position: 'bottom-center' })
        return
      }
      axios.post(`/pad/j/coursewares/${this.courseware.id}/edit`, qs.stringify({ cwName: this.cwName })).then((response) => {
        if (response.data.status === 1) {
          this.$toasted.error(response.data.detail, { duration: 1000, position: 'bottom-center' })
        } else if (response.data.status === 0) {
          if (response.data.cwName) {
            this.courseware.name = response.data.cwName
            this.$store.commit('updateCoursewareName', { cwId: this.courseware.id, name: response.data.cwName })
          }
          this.$toasted.success('修改成功', { duration: 1000, position: 'bottom-center' })
          this.openBasicsEditor = false
        }
      })
    },
    generateGSendPages(cw) {
      const pages = _.concat(cw.pages[3], cw.pages[2], cw.pages[1])
      const gSendItems = []
      for (const page of pages) {
        // 隐藏旧试题组和所有班级都已发送的课件页
        if (page.unsendCourseIds.length === 0 || page.target.kind === 2018) {
          continue
        }
        const needSubmit = page.target.kind === 2025 || page.target.kind === 2035
        const sendItem = {
          page,
          publishType: 1,
          publishTime: null,
          deadline: null,
          openAnswer: false,
          note: '',
          cmTitle: page.target.name,
          switchOn: false,
          sendCourseIds: [],
          needSubmit,
        }
        gSendItems.push(sendItem)
      }
      return gSendItems
    },
    fetchCoursewareData() {
      $('html,body').scrollTop(0)
      axios.get(`/pad/j/coursewares/${this.$route.params.cwId}/detail`).then((response) => {
        this.courseware = response.data.courseware
        this.cwName = response.data.courseware.name
        this.gSendItems = this.generateGSendPages(response.data.courseware)
      })
    },
    unsendCourses(sendCourses) {
      const arr = []
      for (const c of this.courses) {
        let checkIn = false
        for (const sendCourse of sendCourses) {
          if (sendCourse.courseId === c.id) {
            checkIn = true
            break
          }
        }
        if (!checkIn) arr.push(c)
      }
      return arr
    },
    globalApply(itemName = null) {
      if (!itemName) return
      if (itemName === 'course') {
        for (const item of this.gSendItems) {
          item.sendCourseIds = _.intersection(this.globalConfig.sendCourseIds, item.page.unsendCourseIds)
        }
      } else if (itemName === 'publishTime') {
        for (const item of this.gSendItems) {
          item.publishTime = this.globalConfig.publishTime
        }
      } else if (itemName === 'deadline') {
        for (const item of this.gSendItems) {
          item.deadline = this.globalConfig.deadline
        }
      } else if (itemName === 'openAnswer') {
        for (const item of this.gSendItems) {
          item.openAnswer = this.globalConfig.openAnswer
        }
      } else if (itemName === 'note') {
        if (this.globalConfig.note.length >= 100) {
          this.$toasted.error('备注不能超过100个字符。', { duration: 1000, position: 'bottom-center' })
        }
        for (const item of this.gSendItems) {
          item.note = this.globalConfig.note
        }
      }
      this.$toasted.success('全局应用成功', { duration: 1000, position: 'bottom-center' })
    },
    sendFile(page, courseIds) {
      this.singleSendPage = page
      const courses = []
      for (const course of this.courses) {
        if (courseIds.indexOf(course.id) >= 0) {
          courses.push(course)
        }
      }
      this.singleSendCourses = courses
    },
    refetchAndGotoTab() {
      this.fetchCoursewareData()
      this.curTab = 1
      this.globalConfig = {
        publishTime: null,
        deadline: null,
        openAnswer: false,
        sendCourseIds: [],
        note: '',
      }
    },
    sendFileToCourses(event) {
      if (!this.courses.length || !this.courseware) {
        this.$toasted.error('数据还未加载完成或者你没有可发送的教学班', { duration: 1000, position: 'bottom-center' })
      }

      let sendCount = 0
      for (const item of this.gSendItems) {
        if (!item.switchOn || item.sendCourseIds.length === 0) continue
        sendCount++
        if (item.note.length >= 100) {
          this.$toasted.error('备注不能超过100个字符。', { duration: 1000, position: 'bottom-center' })
          return
        }
      }
      $(event.target).addClass('loading')

      for (const item of this.gSendItems) {
        if (!item.switchOn || item.sendCourseIds.length === 0) continue
        const params = {
          targetKind: this.courseware.kind,
          targetId: this.courseware.id,
          courseIds: JSON.stringify(item.sendCourseIds),
          name: item.cmTitle,
          pageId: item.page.id,
          note: item.note,
          publishTime: this.publishTime && new Date(item.publishTime),
          deadline: this.deadline && new Date(item.deadline),
          openAnswer: item.openAnswer,
        }
        axios.post('/pad/j/resource/cm/send_to_course', qs.stringify(params)).then((response) => {
          sendCount--
          if (response.data.status === 1) {
            this.$toasted.error(response.data.detail, {
              duration: 1000,
              position: 'bottom-center',
              onComplete: () => {
                if (sendCount <= 0) {
                  $(event.target).removeClass('loading')
                }
              },
            })
          } else if (response.data.status === 0) {
            if (sendCount <= 0) {
              this.$toasted.success('发送成功', {
                duration: 1000,
                position: 'bottom-center',
                onComplete: () => {
                  $(event.target).removeClass('loading')
                  this.refetchAndGotoTab()
                },
              })
            }
          }
        })
      }
    },
  },
}
</script>
<style lang="less">
  @import "~assets/css/file-icon.less";
  @import "~assets/css/base-variables.less";
  .modal-container .modal-body {
    min-height: 200px;
  }
  .modal-header h3 span {
    font-size: 1rem;
  }
</style>

<style lang="less" scoped>
  @color-grey-600: #757575;
  @color-grey-700: #616161;
  @color-grey: #9E9E9E;
  .ui.button.play-cw {
    .fa-icon {
      margin-right: 0;
      margin-bottom: .5em;
      font-size: 20px;
    }
    span {
      display: block;
      font-size: .9rem;
      font-weight: normal;
    }
    &:hover, &:active, &:focus {
      background-color: #dcdddf;
    }
  }

  .ui.info.popup {
    padding: .5rem .5rem .5rem 0;
    max-width: 30rem;
  }
  .note {
    font-size: .9em;
    font-weight: normal;
    color: #a0abba;
  }
  .container-heading {
    display: flex;
    padding: 1rem;
    img {
      width: 3rem;
      height: 3rem;
    }
    .container-title {
      flex: 1;
      margin: 0;
      padding: 0 1rem;
      font-weight: normal;
    }
  }
  .ui.secondary.pointing.menu {
    margin: 0 1rem 2rem;
  }
  .basic-info {
    margin: 1rem;
    color: #888;
    .item-label {
      display: inline-block;
      width: 6rem;
    }
    .link.link-primary {
      font-size: 1em;
      padding-left: 0;
    }
  }
  .course-label {
    background-color: #f5f5f5;
    padding: .3em .4em;
    font-size: .9em;
    border-radius: 3px;
    color: #0009;
    margin-right: 1em;
    margin-bottom: .5em;
    display: inline-block;
    cursor: pointer;
    &:focus, &:hover, &:active {
      background-color: #42A5F5;
      color: white;
    }
    &.disabled {
      cursor: default;
      pointer-events: none;
    }
  }

  .courseware-panel {
    background-color: white;
    min-height: 90vh;
    margin-left: 1rem;
    position: relative;
    width: ~"calc(100% - 21rem)";
    display: inline-block;
    .table-wrapper {
      margin: 1rem;
      .ui.table td {
        padding: .5em;
        .link.send {
          width: 5em;
        }
      }
    }
  }
  .page-title {
    cursor: pointer;
    line-height: 20px;
    min-width: 18rem;
    p {
      font-size: 14px;
      margin: 0;
      word-break: break-all;
    }
    .pad.icon {
      float: left;
      margin-right: 8px;
      height: 20px;
      width: 20px;
      padding: .2rem;
      border-radius: 2px;
      box-shadow: -1px 1px 2px rgba(0, 0, 0, 0.15);
    }
    &:hover, &:focus, &:active {
      color: #42A5F5;
    }
  }

  .courses-holder {
    max-width: 15rem;
  }

  .status-dot {
    display: inline-block;
    height: 8px;
    width: 8px;
    border-radius: 8px;
    margin-right: 5px;
    &.teal-dot {
      background-color: #00b5ad;
    }
    &.gray-dot {
      background-color: #D3DCE6;
    }
    &.orange-dot {
      background-color: #f2711c;
    }
  }

  .advanced-send {
    margin: 1rem;
  }
  .g-config-item {
    display: flex;
    .g-config-item-label {
      width: 6rem;
    }
    .g-config-item-content {
      flex: 1
    }
    .g-config-item-action {
      width: 8rem;
    }
  }
  .send-config.ui.form {
    margin-bottom: 1.5em;
    .ui.fitted.checkbox {
      font-size: 13px;
      margin-right: 1.5rem;
      margin-top: 3px;
    }
    .item-name {
      margin-right: 10px;
      width: 18rem;
      .item-type {
        margin-top: 0;
      }
    }
    .item-type {
      margin-top: 1rem;
      .link.link-default {
        text-decoration: underline;
      }
    }
    .send-header, .send-body .send-item{
      display: flex;
      border-bottom: solid 1px #EFF2F7;
    }
    .send-header {
      font-weight: bold;
      padding: .5em 0;
      span {
        line-height: 1;
      }
    }
    .send-body {
      .send-item {
        align-items: top;
        .ui.fitted.checkbox {
          margin-top: 18px;
        }
        .item-name {
          margin: 5px 10px 5px 0;
          .ui.mini.input input[type=text]{
            padding: .4em .8em;
            font-size: 1rem;
            color: @color-grey-700;
          }
        }
        .item-setting-detail {
          margin-bottom: -2px;
          border-bottom: solid 2px white;
          flex: 1;
          padding: 1.2rem .8rem;
          .ui.checkbox {
            margin-right: 2em;
            font-size: 12px;
            label {
              color: @color-grey-700;
            }
          }

          background-color: #f8f8f8;
          color: @color-grey-700;
          font-size: 12px;
          .setting-row {
            display: flex;
            align-items: top;
            margin-bottom: .6em;
            .setting-detail-label {
              display: inline-block;
              line-height: 1.8;
              width: 6em;
              color: @color-grey;
            }
            .setting-detail-content {
              flex: 1;
              input[type=text] {
                background-color: transparent;
                padding: .4em 1em;
                color: @color-grey-700;
                max-width: 30rem;
              }
              .cw-time-picker {
                width: 18rem;
              }
              .ui.open-answer.checkbox {

              }
            }
          }
        }
      }
    }
  }
  .g-config-item {
    display: flex;
    color: @color-grey-700;
    font-size: 12px;
    .g-config-item-label {
      padding: 1rem;
      width: 7rem;
      background-color: #f8f8f8;
      border-bottom: white 1px solid;
      border-right: white 1px solid;
    }
    .g-config-item-content {
      padding: 1rem;
      background-color: #f8f8f8;
      border-bottom: white 1px solid;
      border-right: white 1px solid;
      flex: 1;
      .ui.checkbox {
        font-size: 12px;
        margin-right: 2em;
        label {
          color: @color-grey-700;
        }
      }
    }
    .g-config-item-action {
      background-color: #f8f8f8;
      border-bottom: white 1px solid;
      padding: 1rem;
      width: 6rem;
      text-align: right;
    }
  }
</style>



