<template>
<div class="question question-reading" v-if="question">
  <div class="question-stem">
    <span v-if="index">{{index}}. </span>
    <span class="question-kind-label">{{question.kindName}}</span>
    <span v-if="question.score">({{question.score}}分)</span>
    <span v-if="!answerSheetMode" v-html="question.stemHtml" class="richtext"></span>
  </div>
  <div class="sub-questions">
    <div v-for="(subQ, idx) of question.subQuestions" class="sub-question">
      <span class="sub-q-seq-label">({{idx + 1}})</span>
      <div class="sub-q-body">
        <div v-html="subQ.stemHtml" class="richtext"></div>
        <div class="options">
          <div v-for="option in subQ.options" :class="optionClass(option)" :key="option.id">
            <span class="seq-label">{{option.seqLabel}}. </span>
            <span v-html="option.optionHtml" class="richtext"></span>
            <icon v-if="showUserAnswer && option.isUserAnswer(studentId)"
              :name="option.isAnswer?'check':'remove'"></icon>
          </div>
        </div>
      </div>
      <div class="q-stat" v-if="showStat && question.hasReport">
        <div class="stat-chart">
          <circular-chart class="normal content"
            :options="subQ.options"
            :totalStuCount="totalStuCount"></circular-chart>
          <div class="overlay content"></div>
          <button class="ui primary button action content" @click="resultModalTarget=subQ">查看详情</button>
        </div>
      </div>
    </div>
  </div>
  <toggle-analysis :question="question" v-if="!answerSheetMode"></toggle-analysis>
  <modal-objective-results
    :question="resultModalTarget"
    :allStudents="allStudents"
     v-if="showStat && resultModalTarget && resultModalTarget.hasReport"
    @close="resultModalTarget=null"></modal-objective-results>
</div>
</template>

<script>
import CircularChart from 'components/questions/circular-chart'
import ModalObjectiveResults from 'components/questions/modal-objective-results'
import ToggleAnalysis from 'components/questions/toggle-analysis'
import 'components/icons/remove'
import 'components/icons/check'
import Icon from 'components/icons/Icon'

require('assets/css/richtext.less')
require('./question.less')

export default {
  name: 'question-reading',
  props: {
    index: { type: Number, default: 0 },
    question: { type: Object, required: true },
    showStat: { type: Boolean, required: false, default: false },
    showAnswer: { type: Boolean, required: false, default: true },
    showUserAnswer: { type: Boolean, required: false, default: false },
    studentId: { type: Number, default: null },
    answerSheetMode: { type: Boolean, required: false, default: false },
    allStudents: { type: Object, required: false, default: null },
  },
  data() {
    return {
      resultModalTarget: null,
    }
  },
  components: { CircularChart, ModalObjectiveResults, ToggleAnalysis, Icon },
  computed: {
    totalStuCount() {
      if (!this.allStudents) return 0
      return Object.keys(this.allStudents).length
    },
  },
  methods: {
    optionClass(option) {
      const classes = ['option']
      if (option.isAnswer) {
        classes.push('option-correct')
      }
      if (this.showUserAnswer) {
        classes.push('with-user')
        const isUserAnswer = option.isUserAnswer(this.studentId)
        if (isUserAnswer) {
          classes.push('user-answer')
        }
        if (!option.isAnswer && isUserAnswer) {
          classes.push('option-wrong')
        }
      }
      return classes.join(' ')
    },
  },
}
</script>

<style lang="less" scoped>
.question-reading {
  .sub-question {
    margin-top: .5em;
    display: flex;
    .sub-q-body {
      flex: 1;
    }
    .q-stat {
      text-align: right;
    }
    .sub-q-seq-label {
      width: 2rem;
    }
  }
}
</style>



