<template>
  <div class="board-list" id="board-list">
    <div v-for="date_ of dates" class="gallary">
      <h4>{{date_}}</h4>
      <div class="board-flow">
        <div v-for="board, index in dateBoards[date_]" class="image">
          <img :src="board.imgUrl + '?x-oss-process=style/web-board'" alt="板书">
          <div class="overlay">
            <button class="ui icon circular button" @click="previewItem=board"><icon name="slide-show"></icon></button>
            <button class="ui icon circular button" @click="deleteItem=board"><icon name="remove"></icon></button>
          </div>
        </div>
      </div>
    </div>
    <h4></h4>
    <infinite-loading
      force-use-infinite-wrapper="true"
      spinner="waveDots"
      @infinite="infiniteHandler">
        <span slot="no-more"></span>
        <span slot="no-results"> 还没有板书呢 :(</span>
    </infinite-loading>
    <modal v-if="previewItem" class="modal-massive item-preview" distance="10"
      @close="previewItem=null"
      :withFooter="false">
      <h3 slot="header">预览：<span>{{ previewItem.name }}</span></h3>
      <template slot="body">
        <page-image :fileUrl="previewItem.imgUrl"></page-image>
      </template>
    </modal>
    <modal v-if="deleteItem" class="item-delete" :withHeader="false" @close="deleteItem=null">
      <div slot="body">
        <span class="ui text-center circular label">
          <i class="ui red warning icon"></i>
        </span>
        <p class="alert-text">确认要删除这个板书吗？删除后不可恢复。</p>
      </div>
      <div slot="footer">
        <button class="ui basic button" @click="deleteItem=null">取消</button>
        <button class="ui red button" @click="actionDelItem()">删除</button>
      </div>
    </modal>
  </div>
</template>

<script>
import axios from 'axios'
import PageImage from 'components/doc/image'
import Modal from 'components/modal'
import Icon from 'components/icons/Icon'
import 'components/icons/slide-show'
import 'components/icons/remove'

const qs = require('qs')

export default {
  name: 'board-list',
  props: {},
  data() {
    return {
      loading: false,
      dateBoards: {},
      start: 0,
      dates: [],
      previewItem: null,
      deleteItem: null,
    }
  },
  components: {
    InfiniteLoading: () => import(/* webpackChunkName: "vue-infinite-loading" */ 'vue-infinite-loading'),
    Icon,
    PageImage,
    Modal,
  },
  mounted() {
  },
  methods: {
    actionDelItem() {
      if (!this.deleteItem) return

      const params = { boardId: this.deleteItem.id }
      axios.post('/pad/j/teach/boards/remove', qs.stringify(params)).then((response) => {
        if (response.data.status === 0) {
          const index = this.dateBoards[this.deleteItem.date].indexOf(this.deleteItem)
          this.dateBoards[this.deleteItem.date].splice(index, 1)
          this.deleteItem = null
          this.$toasted.error('删除成功', { duration: 800, position: 'top-center' })
        } else if (response.data.status === 1) {
          this.$toasted.error(response.data.detail, { duration: 800, position: 'top-center' })
        }
      })
    },
    infiniteHandler($state) {
      const params = { start: this.start, limit: 10 }
      axios.get('/pad/j/teach/boards/list', { params }).then((response) => {
        if (response.data.status === 0) {
          const boards = response.data.boards
          if (boards.length === 0) {
            $state.complete()
          } else {
            for (const b of boards) {
              if (this.dates.indexOf(b.date) < 0) {
                this.dates.push(b.date)
                this.dateBoards[b.date] = [b]
              } else {
                this.dateBoards[b.date].push(b)
              }
            }
            this.start += boards.length
            $state.loaded()
          }
        }
      })
    },
  },
}
</script>

<style lang="less">
@import "~assets/css/base-variables.less";
.modal-header h3 span {
  font-size: 1rem;
}
.item-edit .modal-container .modal-body {
   min-height: 150px;
}
.modal-massive.item-preview {
  .modal-body {
    padding: 0;
    min-height: 10vh;
    height: 80vh;
  }
  &.page-image .modal-container {
    width: 900px;
    .modal-body {
      overflow-y: hidden;
      height: 800px;
    }
  }
  .viewer-container {
    background-color: #00000010;

  }
}
.item-delete {
  text-align: center;
}
.item-delete .ui.circular.label {
  border: solid 1px #dd4b39;
  border-radius: 3em;
  font-size: 2em;
  width: 1.8em;
  height: 1.8em;
  background-color: white;
  .ui.icon {
    margin: 0;
  }
}
</style>


<style lang="less" scoped>
@color-blue: #42A5F5;

@media (min-width: 1400px)
{
  .gallary .image {
    width: ~"calc(100% / 6 - 30px)";
  }
}
@media (max-width: 1400px) and (min-width: 800px)
{
  .gallary .image {
    width: ~"calc(100% / 5 - 30px)";
  }
}
@media (max-width: 800px)
{
  .gallary .image {
    width: ~"calc(100% / 3 - 30px)";
  }
}

.board-list {
  min-height: 100vh;
}

.gallary {
  padding: 0 2rem;
  h4 {
    padding-left: 8px;
    font-size: 15px;
    font-weight: normal;
    border-left: solid 4px #42A5F5;
  }
  .board-flow {
    display: flex;
    flex-wrap: wrap;
    margin: 0 -1rem;
    .image {
      margin: 0 15px 30px 15px;
      padding: .5rem;
      background-color: white;
      box-shadow: 0 1px 2px rgba(0,0,0,0.07);
      min-height: 10rem;
      img {
        width: 100%;
      }
      position: relative;
      .overlay {
        display: none;
        position: absolute;
        top: 0;
        bottom: 0;
        left: 0;
        right: 0;
        background-color: rgba(0, 0, 0, 0.5);
        color: white;
        text-align: center;
        vertical-align: middle;
        font-size: 2rem;
        padding-top: 40px;
        .ui.button {
          margin: 0 1rem;
          font-size: 20px;
          width: 2.8rem;
          height: 2.8rem;
          padding: 0 0 .2rem 0;
          display: inline-block;
          background-color: transparent;
          color: white;
          .fa-icon {
            margin-right: 0;
          }
          border-radius: 18rem;
        }
        .ui.button:hover, .ui.button:active, .ui.button:focus {
          cursor: pointer;
          background-color: rgba(255, 255, 255, 0.4);
        }
      }
    }
    .image:focus, .image:hover {
      .overlay {
        display: block;
      }
    }
  }
}


</style>



