import LongDialog from 'assets/js/questions/long-dialog'
import Cloze from 'assets/js/questions/cloze'
import SingleChoice from 'assets/js/questions/single-choice'
import Composition from 'assets/js/questions/composition'
import ChoiceWithOptionPool from 'assets/js/questions/choice-with-option-pool'
import Blank from 'assets/js/questions/blank'

import MathChoice from 'assets/js/questions/math-choice'
import MathResponse from 'assets/js/questions/math-response'

import ScienceChoice from 'assets/js/questions/science-choice'
import ScienceResponse from 'assets/js/questions/science-response'
import LiberalMaterial from 'assets/js/questions/liberal-material'

import ReadingView from 'components/questions/reading'
import LongDialogView from 'components/questions/long-dialog'
import ClozeView from 'components/questions/cloze'
import SingleChoiceView from 'components/questions/single-choice'
import CompositionView from 'components/questions/composition'
import ChoiceWithOptionPoolView from 'components/questions/choice-with-option-pool'
import BlankView from 'components/questions/blank'

import MathChoiceView from 'components/questions/math-choice'
import MathResponseView from 'components/questions/math-response'

import ScienceResponseView from 'components/questions/science-response'
import LiberalMaterialView from 'components/questions/liberal-material'

import NqObjective from 'assets/js/questions/nq-objective'
import NqMultiChoice from 'assets/js/questions/nq-multi-choice'
import NqSubjective from 'assets/js/questions/nq-subjective'

export const QuestionKindToMV = {
  1012: { model: LongDialog, view: LongDialogView, viewName: 'short-dialog-view' },  // 听力短对话
  1014: { model: LongDialog, view: LongDialogView, viewName: 'long-dialog-view' },
  1017: { model: SingleChoice, view: SingleChoiceView, viewName: 'single-chioce-view' },
  1019: { model: Composition, view: CompositionView, viewName: 'composition-view' },
  1020: { model: Reading, view: ReadingView, viewName: 'reading-view' },
  1047: { model: Cloze, view: ClozeView, viewName: 'cloze-view' },
  1034: { model: ChoiceWithOptionPool, view: ChoiceWithOptionPoolView, viewName: 'choice-with-option-pool-view' },
  1045: { model: Blank, view: BlankView, viewName: 'passage-blank-view' }, // 短文填词
  1083: { model: Blank, view: BlankView, viewName: 'sentence-view' }, // 完成句子

  // // 数学
  2000: { model: MathChoice, view: MathChoiceView, viewName: 'math-chioce-view' },
  2002: { model: MathResponse, view: MathResponseView, viewName: 'math-blank-view' },
  2006: { model: MathResponse, view: MathResponseView, viewName: 'math-response-view' },

  // // 化学
  7100: { model: ScienceChoice, view: MathChoiceView, viewName: 'chemistry-chioce-view' },
  7110: { model: MathResponse, view: MathResponseView, viewName: 'chemistry-blank-view' },
  7120: { model: ScienceResponse, view: ScienceResponseView, viewName: 'chemistry-response-view' },

  // // 物理
  7200: { model: ScienceChoice, view: MathChoiceView, viewName: 'physics-chioce-view' },
  7210: { model: MathResponse, view: MathResponseView, viewName: 'physics-blank-view' },
  7220: { model: ScienceResponse, view: ScienceResponseView, viewName: 'physics-response-view' },

  // // 生物
  7000: { model: ScienceChoice, view: MathChoiceView, viewName: 'biology-chioce-view' },
  7010: { model: MathResponse, view: MathResponseView, viewName: 'biology-blank-view' },
  7020: { model: ScienceResponse, view: ScienceResponseView, viewName: 'biology-response-view' },

  // // 语文
  8000: { model: SingleChoice, view: SingleChoiceView, viewName: 'chinese-chioce-view' },
  8010: { model: Reading, view: ReadingView, viewName: 'chinese-reading-view' },
  8020: { model: LiberalMaterial, view: LiberalMaterialView, viewName: 'chinese-material-view' },
  8030: { model: Composition, view: CompositionView, viewName: 'composition-composition-view' },

  // // 历史
  8100: { model: SingleChoice, view: SingleChoiceView, viewName: 'history-chioce-view' },
  8110: { model: Reading, view: ReadingView, viewName: 'history-reading-view' }, // 目前实际上没有历史阅读题
  8120: { model: LiberalMaterial, view: LiberalMaterialView, viewName: 'history-material-view' },

  // // 地理
  8200: { model: SingleChoice, view: SingleChoiceView, viewName: 'geography-chioce-view' },
  8210: { model: Reading, view: ReadingView, viewName: 'geography-reading-view' }, // 目前实际上没有历史阅读题
  8220: { model: LiberalMaterial, view: LiberalMaterialView, viewName: 'geography-material-view' },

  // // 政治
  8300: { model: SingleChoice, view: SingleChoiceView, viewName: 'politics-chioce-view' },
  8310: { model: Reading, view: ReadingView, viewName: 'politics-reading-view' }, // 目前实际上没有历史阅读题
  8320: { model: LiberalMaterial, view: LiberalMaterialView, viewName: 'politics-material-view' },
}


export function getQuestionByData(questionData) {
  let QuestionModel = null
  if (questionData.kind === 2026) {
    QuestionModel = questionData.multiAnswer ? NqMultiChoice : NqObjective
  }
  else if (questionData.kind === 2027) {
    QuestionModel = NqSubjective
  } else {
    QuestionModel = QuestionKindToMV[questionData.kind].model
  }
  const params = QuestionModel.extractParams(questionData)
  return new QuestionModel(params)
}



