<template>
  <div v-if="courses.length" class="start-stop-lesson">
    <div>
      <button class="ui tiny primary button" @click="openStartTeachingModal=true" v-if="!teachingCourseId">
        <i class="icon play"></i>开始上课</button>
      <template v-if="teachingCourse">
        {{teachingCourse.name}} {{teachingTimeDelta}}
        <button class="ui tiny primary button" @click="stopTeaching()">
          <i class="icon stop"></i>下课</button>
        <button class="ui tiny primary button" v-if="!controlScreen" @click="startControlScreen()" title="控制学生平板（锁屏）">
          <i class="icon play"></i>控屏</button>
        <button class="ui tiny primary button" v-if="controlScreen" @click="stopControlScreen()" title="解除控制学生平板">
          <i class="icon stop"></i>解除控屏</button>
        <button class="ui tiny primary button" @click="openRandomCallModal=true">随机点名</button>
      </template>
    </div>
    <modal v-if="openStartTeachingModal" @close="openStartTeachingModal=false" class="choose-teaching-course">
      <h3 slot="header">开始上课</h3>
      <div slot="body">
        <form class="ui form">
          <div class="grouped fields required">
            <label>选择要上课的教学班</label>
            <div class="field radio-checkbox" v-for="course of courses">
              <div class="ui radio checkbox">
                <input type="radio" :value="course.id" v-model="chooseTeachingCourseId" :id="'select-course-'+course.id">
                <label :for="'select-course-'+course.id">{{course.name}}</label>
              </div>
            </div>
          </div>
        </form>
      </div>
      <div slot="footer">
        <button class="ui mini blue button" @click="startTeaching" :class="chooseTeachingCourseId?'':'disabled'">确定</button>
      </div>
    </modal>
    <modal v-if="openRandomCallModal" @close="openRandomCallModal=false" class="randomCallModal">
      <h3 slot="header">随机点名</h3>
      <template v-if="students.length >= 2">
        <h3 slot="body" class="studentName">
          <div class="studentMask">
            <div class="line"></div>
            <div class="line"></div>
          </div>
          <div class="callContainer" :class="isScroll ? 'scroll' : ''">
            <div v-for="(item, index) in randomCallStudents" :key="`${index}-${item.id}`">
              {{item.name}}
            </div>
          </div>
        </h3>
        <div slot="footer">
          <button class="ui mini blue button" @click="randomCall()">点名</button>
        </div>
      </template>
      <h3 slot="body" v-if="students.length === 1">此班级仅 {{students[0].name}} 一名学生</h3>
      <h3 slot="body" v-if="!students.length">此班级没有学生可供点名</h3>
    </modal>
  </div>
</template>

<script>
import { mapState } from 'vuex'
import axios from 'axios'
import Modal from 'components/modal'
import { PadDateTime } from 'assets/js/utils'

import mixin from '../mixin/'

const qs = require('qs')

export default {
  name: 'start-stop-lesson',
  mixins: [mixin],
  data() {
    return {
      openStartTeachingModal: false,
      chooseTeachingCourseId: 0,
      now: new Date(),
      setIntervalId: null,
      controlScreen: false,
      students: [],
      randomCallStudents: [],
      openRandomCallModal: false,
      isScroll: false,
    }
  },
  mounted() {
    this.getTeachingCourseId()
    if (this.teachingCourseId) {
      this.setIntervalId = window.setInterval(() => { this.now = new Date() }, 1000)
    }
  },
  components: { Modal },
  watch: {
    teachingCourseId(val, oldVal) {
      if (val && !oldVal && !this.setIntervalId) {
        this.setIntervalId = window.setInterval(() => { this.now = new Date() }, 1000)
      } else if (oldVal && !val && this.setIntervalId) {
        window.clearInterval(this.setIntervalId)
        this.setIntervalId = 0
      }
    },
  },
  computed: {
    teachingCourse() {
      if (!this.teachingCourseId || !this.courses.length) {
        return null
      }
      for (const course of this.courses) {
        if (course.id === this.teachingCourseId) {
          return course
        }
      }
    },
    teachingTimeDelta() {
      if (!this.teachingStartTime || this.teachingStartTime > this.now) return ''

      const padStartTime = new PadDateTime(this.teachingStartTime)
      return padStartTime.formatTimeDelta(this.now)
    },
    ...mapState(['courses', 'teachingCourseId', 'teachingStartTime', 'accessToken']),
  },
  methods: {
    startTeaching() {
      if (!this.chooseTeachingCourseId) {
        this.$toasted.error('请选择教学班', { duration: 1000, position: 'bottom-center' })
      }
      axios.post('/pad/j/teach/start', qs.stringify({ courseId: this.chooseTeachingCourseId })).then((response) => {
        if (response.data.status === 1) {
          this.$toasted.error(response.data.detail, { duration: 1000, position: 'bottom-center' })
        } else if (response.data.status === 0) {
          this.getTeachingCourseId()
          this.openStartTeachingModal = false
          this.$nextTick(() => {
            axios.get('/pad/j/teach/students', { params: { courseId: this.chooseTeachingCourseId } }).then(({ data }) => {
              const { students, status, detail } = data
              if (status) {
                this.$toasted.error(detail, { duration: 1000, position: 'bottom-center' })
              } else {
                this.students = Object.values(students)
                this.genRandomStudents()
              }
            })
          })
        }
      })
    },
    genRandomStudents() {
      const { students } = this
      if (students.length < 2) return
      const shuffleResult = this.shuffle(students)
      const loopCount = Math.ceil(100 / students.length)
      let loopResult = []
      for (let i = 0; i < loopCount; i += 1) {
        loopResult = loopResult.concat(...shuffleResult)
      }
      this.randomCallStudents = loopResult
    },
    randomCall() {
      this.isScroll = false
      this.statistic({
        course: this.chooseTeachingCourseId,
      }, {
        headers: { Authorization: this.accessToken },
      })
      this.genRandomStudents()
      this.$nextTick(() => {
        window.setTimeout(() => {
          this.isScroll = true
        }, 0)
      })
    },
    shuffle(arr) {
      const { length } = arr
      const arrCopy = arr.concat()
      for (let i = 0; i < length; i++) {
        const j = Math.floor(Math.random() * (i + 1))
        const temp = arrCopy[i]
        arrCopy[i] = arrCopy[j]
        arrCopy[j] = temp
      }
      return arrCopy
    },
    stopTeaching() {
      if (!this.teachingCourseId) return
      axios.post('/pad/j/teach/stop').then((response) => {
        if (response.data.status === 0) {
          this.getTeachingCourseId()
          this.chooseTeachingCourseId = 0
          this.stopControlScreen()
        }
      })
    },
    startControlScreen() {
      if (!this.teachingCourseId) return

      axios.post('/pad/j/teach/screen_control', qs.stringify({ courseId: this.teachingCourseId, controlScreen: 0 })).then((response) => {
        if (response.data.status === 0) {
          this.controlScreen = true
        }
      })
    },
    stopControlScreen() {
      if (!this.teachingCourseId) return

      axios.post('/pad/j/teach/screen_control', qs.stringify({ courseId: this.teachingCourseId, controlScreen: 1 })).then((response) => {
        if (response.data.status === 0) {
          this.controlScreen = false
        }
      })
    },
    getTeachingCourseId() {
      axios.get('/pad/j/teach/teaching_course_id').then((response) => {
        this.$store.commit('updateTeachingCourse', {
          courseId: response.data.courseId,
          startTime: new Date(response.data.startTime),
        })
      })
    },
  },
}
</script>

<style lang="less">
@import './../../../assets/css/base-variables.less';
.start-stop-lesson {
  display: flex;
  align-items: center;
  color: #666;
  font-size: 13px;
  button {
    margin-left: 0.5em;
  }
  .randomCallModal {
    .modal-body {
      min-height: auto;
      display: flex;
      justify-content: center;
      font-size: 30px;
    }
    .studentName {
      position: relative;
      text-align: center;
      min-width: 100px;
      height: 150px;
      line-height: 50px;
      overflow: hidden;
      padding: 0 18px;
      .studentMask {
        display: flex;
        flex-direction: column;
        justify-content: space-evenly;
        position: absolute;
        left: 0;
        top: 0;
        width: 100%;
        height: 100%;
        padding: 0 18px;
        background: linear-gradient(
          rgba(255, 255, 255, 0.9),
          rgba(0, 0, 0, 0),
          rgba(255, 255, 255, 0.9)
        );
        z-index: 1;
        .line {
          background: @color-blue;
          flex-basis: 1px;
          flex-grow: 0;
        }
        &::before {
          content: '';
          width: 0;
          height: 0;
          position: absolute;
          left: 0px;
          top: 50%;
          transform: translateY(-50%);
          border-left: 16px solid @color-blue;
          border-bottom: 8px solid transparent;
          border-top: 8px solid transparent;
        }
      }
    }
    .callContainer {
      margin: 0 10px;
    }
    .scroll {
      transition: 5s all ease;
      transform: translateY(calc(~'-100% + 150px'));
    }
  }
}
</style>



