function textRender(item) {
  if (item.formats.length > 0) {
    return `<span class="${item.formats.join(' ')}">${item.text}</span>`
  }
  return `${item.text}`
}


function formulaRender(item) {
  if (item.inline) {
    return String.raw`<span class="formula">\(${item.text}\)</span>`
  }
  return String.raw`<span class="formula">$$${item.text}$$</span>`
}


function placeholderRender(item) {
  let content = '&nbsp;'
  if (item.subtype === 'option') {
    content = `${item.index}`
  } else if (item.subtype === 'bracket') {
    content = '(&nbsp;)'
  }
  return `<span class="placeholder ${item.subtype}" data-index="${item.index}">${content}</span>`
}

function imageRender(item) {
  return `<div class="image" data-id="${item.id}">
            <img src="${item.url}" style="height:${item.height}px;width:${item.width}px;max-width:100%;" alt="${item.description}" title="${item.description}" />
          </div>`
}

const renders = {
  text: textRender,
  formula: formulaRender,
  newline: () => '<br>',
  placeholder: placeholderRender,
  image: imageRender,
  annotation: item => `<span class="annotation">${item.text}</span>`,
  link: item => `<span class="link"><a href="${item.url}" target="_blank">${item.text}</a></span>`,
}


function parsedRtToHtml(parsedRt) {
  const htmlArr = []
  for (const item of parsedRt) {
    htmlArr.push(renders[item.type](item))
  }
  return `${htmlArr.join('')}`
}

export default parsedRtToHtml



