<template>
<div class="image-viewer-container">
    <loader v-if="loading"></loader>
    <img :src="fileUrl" class="image">
</div>
</template>

<script>
const $script = require('scriptjs')
import Loader from 'components/loader'

export default {
  name: 'page-image',
  props: {
    fileUrl: {
      type: String,
    },
  },
  data() {
    return {
      viewer: null,
      loading: true,
    }
  },
  components: { Loader },
  mounted() {
    const image = this.$el.querySelector('.image')
    image.style.display = 'none'

    image.addEventListener('ready', () => {
      this.loading = false
    })

    $script('/static/w/assets/viewer/viewer.js', () => {
      this.viewer = new Viewer(image, {
        inline: true,
        button: false,
        navbar: false,
        title: false,
        scalable: false,
        keyboard: false,
        toolbar: {
          zoomIn: 4,
          zoomOut: 4,
          reset: 1,
          prev: 0,
          play: 0,
          next: 0,
          rotateLeft: 1,
          rotateRight: 1,
          flipHorizontal: 0,
          flipVertical: 0,
        },
        playControl: false,
        transition: false,
      })
      this.viewer.onWheel = null
    })
  },
  beforeDestroy() {
    if (this.viewer) {
      this.viewer.destroy()
    }
  },
  watch: {
    fileUrl() {
      this.$nextTick().then(() => { this.viewer.update() })
    },
  },
}
</script>

<style>
  @import "/static/w/assets/viewer/viewer.css";
</style>
<style lang="less">

.viewer-container {
  background-color: rgba(0, 0, 0, .3);
}
</style>

<style lang="less" scoped>
.image-viewer-container {
  height: 100%;
  width: 100%;
  position: relative;
}
#image-viewer {
  width: auto;
  height: auto;
  margin: auto;
  display: block;
}

</style>



