<template>
<modal @close="$emit('close')" class="modal-large modal-file-sender" key="file-sender-modal" :withFooter="false">
  <h3 slot="header">发送资料：<span>{{page.target.name}}</span></h3>
  <div slot="body" class="modal-body-container">
    <span class="text-danger" v-if="isOldPaper">此为旧试题组类型，已经不支持发送给学生作答。请使用新题库和新试题组。</span>
    <form class="ui form" v-else>
      <div class="setting-row">
        <span class="setting-label name-row">作业名称*</span>
        <div class="setting-content">
          <div class="ui mini input">
            <input type="text" v-model="cmTitle" placeholder="不超过100个字符">
          </div>
          <span class="note">{{classKindMap[page.classKind]}} / </span>
          <span class="note">{{resourceTypeNameMap[page.target.fileType]}}</span>
        </div>
      </div>
      <div class="setting-row">
        <span class="setting-label">发布时间</span>
        <time-picker
          place-holder-text="立即发送"
          :key="page.id + '-s-publishtime-picker'"
          v-model="publishTime"
          :minDate="minDate"
          :maxDate="maxDate"
          class="setting-content"></time-picker>
      </div>
      <div class="setting-row" v-if="needSubmit">
        <span class="setting-label">收卷时间</span>
        <time-picker
          place-holder-text="未设置（可不填）"
          :key="page.id + '-s-deadline-picker'"
          v-model="deadline"
          :initOffsetMinutes="10"
          :minDate="minDate"
          :maxDate="maxDate"
          class="setting-content"></time-picker>
      </div>
      <div class="setting-row" v-if="needSubmit">
        <span class="setting-label"></span>
        <div class="setting-content">
          <div class="ui checkbox">
            <input type="checkbox" id="cm-open-answer" tabindex="0" class="hidden" v-model="openAnswer">
            <label for="cm-open-answer">开放答案<span class="text-muted">（学生提交后立即展示正确答案）</span></label>
          </div>
        </div>
      </div>
      <div class="setting-row">
        <span class="setting-label">发送目标</span>
        <div class="setting-content">
          <span v-if="courses.length === 1">{{courses[0].name}}</span>
          <div v-else class="send-courses">
            <div v-for="course of courses" :key="course.id" class="course-block">
              <div class="ui course checkbox" >
                <input type="checkbox" tabindex="0" class="hidden" :value="course.id"
                  :id="'course-cb-'+course.id" v-model="sendCourseIds">
                <label :for="'course-cb-'+course.id">{{course.name}}</label>
              </div>
            </div>
          </div>
        </div>
      </div>
      <div class="setting-row">
        <span class="setting-label"></span>
        <div class="setting-content">
          <span class="text-danger" v-if="isNewPaper">此为新试题组，目前老师只能在电脑端查看和批改该类型的作业。教师平板端适配即将上线。</span>
        </div>
      </div>
      <div class="setting-row">
        <span class="setting-label">备注</span>
        <div class="setting-content">
          <div class="ui mini input">
            <input type="text" v-model="note" placeholder="学生可见（选填），不超过100个字符">
          </div>
        </div>
      </div>
       <div class="setting-row">
        <span class="setting-label"></span>
        <div class="setting-content">
          <button class="ui tiny blue button" @click.prevent="sendFile($event)">发送</button>
        </div>
      </div>
    </form>
  </div>
</modal>
</template>

<script>
import Modal from 'components/modal'
import axios from 'axios'
import {
  BEFORE_CLASS,
  IN_CLASS,
  AFTER_CLASS,
  RESOURCE_TYPE_NAME_MAP,
  K_PAD_PAPER,
  K_PAD_NEW_QUESTION,
  K_NEW_QUESTION_BANK_PAPER,
} from 'assets/js/consts/common'
import _ from 'lodash'

const qs = require('qs')

export default {
  name: 'file-sender',
  props: {
    page: { type: Object, required: true },
    courses: {
      type: Array,
      required: true,
      validator(value) {
        return value.length > 0
      },
    },
    courseware: { type: Object, required: true },
  },
  data() {
    return {
      classKindMap: {
        [BEFORE_CLASS]: '课前',
        [IN_CLASS]: '课中',
        [AFTER_CLASS]: '课后',
      },
      cmTitle: this.page.target.name,
      note: '',
      publishTime: null,
      deadline: null,
      openAnswer: false,
      resourceTypeNameMap: RESOURCE_TYPE_NAME_MAP,
      sendCourseIds: _.map(this.courses, 'id'),
    }
  },
  components: {
    Modal,
    TimePicker: () => import('components/time-picker'),
  },
  computed: {
    needSubmit() {
      return this.page.target.kind === K_PAD_NEW_QUESTION || this.page.target.kind === K_NEW_QUESTION_BANK_PAPER
    },
    minDate: () => new Date(),
    maxDate: () => {
      let max = new Date()
      max.setDate(max.getDate() + 15)
      return max
    },
    isOldPaper() {
      return this.page.target.kind === K_PAD_PAPER
    },
    isNewPaper() {
      return this.page.target.kind === K_NEW_QUESTION_BANK_PAPER
    },
  },
  methods: {
    sendFile(event) {
      if (!this.sendCourseIds.length) {
        this.$toasted.error('没有可发送的目标', { duration: 1000, position: 'bottom-center' })
      }

      if (this.cmTitle.length === 0 || this.cmTitle.length >= 100 || this.note.length >= 100) {
        this.$toasted.error('作业名称必填；名称和备注不能超过100个字符。', { duration: 1000, position: 'bottom-center' })
        return
      }
      $(event.target).addClass('loading')

      const params = {
        targetKind: this.courseware.kind,
        targetId: this.courseware.id,
        courseIds: JSON.stringify(this.sendCourseIds),
        name: this.cmTitle,
        pageId: this.page.id,
        note: this.note,
        publishTime: this.publishTime && new Date(this.publishTime),
        deadline: this.deadline && new Date(this.deadline),
        openAnswer: this.openAnswer,
      }
      axios.post('/pad/j/resource/cm/send_to_course', qs.stringify(params)).then((response) => {
        if (response.data.status === 1) {
          this.$toasted.error(response.data.detail, {
            duration: 1000,
            position: 'bottom-center',
            onComplete: () => {
              $(event.target).removeClass('loading')
            },
          })
        } else if (response.data.status === 0) {
          $(event.target).removeClass('loading')
          this.$emit('success')
          this.$emit('close')
          this.$toasted.success('发送成功', { duration: 800, position: 'bottom-center' })
        }
      })
    },
  },
}
</script>

<style lang="less">
@import "~assets/css/base-ui-component.less";

.modal-file-sender {
  &.modal-large .modal-container{
    width: 45rem;
  }
  .ui.form {
    margin: 1rem 0;
  }
  .setting-row {
    display: flex;
    font-size: 13px;
    margin-bottom: 1.5em;
    .setting-label {
      width: 6em;
      font-weight: bold;
      &.name-row {
        margin-top: .4em;
      }
    }
    .setting-content {
      flex: 1;
      color: @font-color-base-light;
      .ui.mini.input input[type=text]{
        padding: .4em .8em;
        color: @color-grey-700;
        font-size: 13px;
        width: 30rem;
      }
      .course-block {
        margin-right: 1rem;
        display: inline-block;
      }
    }
    .ui.checkbox label{
      font-size: 13px;
      color: @font-color-base-light;
    }
  }
}
</style>



