
class NqObjectiveOption {
  constructor({ seqId, isAnswer = false, isTrueOrFalse }) {
    this.id = seqId
    this.seqId = seqId
    if (isTrueOrFalse) {
      this.seqLabel = this.seqId === 1 ? '对' : '错'
    } else {
      this.seqLabel = String.fromCharCode(64 + seqId)
    }
    this.isAnswer = isAnswer
    this.hasReport = false
  }

  injectReportData(data) {
    this.sids = data[this.seqId] || []
    this.hasReport = true
  }

  isUserAnswer(sid) {
    if (this.hasReport) {
      return this.sids.indexOf(sid) >= 0
    }
  }
}

export default class NqObjective {
  constructor({ id, seqId, kind, note, score, optionCount, answer, isTrueOrFalse, multiAnswer }) {
    this.id = id
    this.seqId = seqId
    this.kind = kind
    if (isTrueOrFalse) {
      this.kindName = '判断题'
    } else if (multiAnswer) {
      this.kindName = '多选题'
    } else {
      this.kindName = '单选题'
    }
    this.key = `key-${kind}-${id}`
    this.note = note
    this.score = score
    this.answer = answer
    if (isTrueOrFalse) {
      this.answerLabel = this.answer === 1 ? '对' : '错'
    } else {
      this.answerLabel = String.fromCharCode(64 + answer)
    }

    this.options = []

    for (let idx = 1; idx <= optionCount; idx++) {
      const option = new NqObjectiveOption({ seqId: idx, isAnswer: idx === answer, isTrueOrFalse })
      this.options.push(option)
    }

    this.hasReport = false
  }

  static extractParams(sourceData) {
    return {
      id: sourceData.seqId,
      seqId: sourceData.seqId,
      kind: sourceData.kind,
      note: sourceData.note,
      score: sourceData.score,
      optionCount: sourceData.optionCount,
      answer: sourceData.answer,
      isTrueOrFalse: sourceData.isTrueOrFalse || false,
    }
  }

  injectReportData(data) {
    if (data.kind !== this.kind || data.seqId !== this.seqId) {
      return
    }
    this.unansweredSids = data.unansweredSids || []
    for (const option of this.options) {
      option.injectReportData(data.results)
    }

    this.hasReport = true
  }
}



